import {List} from "immutable";
import * as React from 'react';
import {FormEvent, useEffect, useState} from 'react';
import {withNamespaces, WithNamespaces} from "react-i18next";
import {Button, ButtonGroup} from "semantic-ui-react";
import {useElementsTree} from "../../../hooks/useElementsTree";
import {useGraph} from "../../../hooks/useGraph";
import {ElementsTree} from "../../../model/ElementsTree";
import {
  convertNodeToJs,
  Graph,
  makeNodeFromJs,
  NextToPosition,
  Node,
  NodeMetadata,
  NodeType
} from "../../../model/Graph";
import {getCommandMetadata} from "../../../service/cody-wizard/command/get-command-metadata";
import {
  dddActionFromNode,
  ImmutableDDDWizardContext,
  isDDDAction
} from "../../../service/cody-wizard/context/ddd-action";
import {ImmutableUiContext, uiContextFromNode} from "../../../service/cody-wizard/context/ui";
import {updateContext} from "../../../service/cody-wizard/context/wizard-context";
import {detectService} from "../../../service/cody-wizard/node/detect-service";
import {getSourcesOfType} from "../../../service/cody-wizard/node/get-single-node-of-type";
import {markAsConnected} from "../../../service/cody-wizard/node/mark-as-connected";
import {names} from "../../../service/cody-wizard/node/names";
import {Schema} from "../../../service/cody-wizard/schema/schema";
import {
  ensurePluralNoun,
  ensureSingularNoun,
  isAdjective,
  isNoun,
  tagSentence
} from "../../../service/cody-wizard/tagger";
import {getUiMetadata, UiMetadata} from "../../../service/cody-wizard/ui/get-ui-metadata";
import {getSchemaRefName} from "../../../service/cody-wizard/vo/get-schema-ref-name";
import {getVoMetadata, PageLinkTableColumn, ValueObjectMetadata} from "../../../service/cody-wizard/vo/get-vo-metadata";
import {isListVo} from "../../../service/cody-wizard/vo/is-list-vo";
import {isStateVo} from "../../../service/cody-wizard/vo/is-state-vo";
import EditorDivider from "../Editor/EditorDivider";
import UiConfigEditor from "../Editor/UiConfigEditor";
import ChangeNodeNameOrDeleteNode from "../Node/ChangeNodeNameOrDeleteNode";
import MultipleNodeSelect from "../Node/MultipleNodeSelect";
import NewNodeInput from "../Node/NewNodeInput";
import NodeSelect from "../Node/NodeSelect";
import {DEFAULT_CONNECT_MARGIN, WithWizardStepProps} from "../WizardModal";
import UiAccessibleFromSelect, {AccessibleFrom} from "./Ui/UiAccessibleFromSelect";
import {repositionFeature} from "./Feature";

interface OwnProps {

}

type UiProps = OwnProps & WithWizardStepProps<ImmutableDDDWizardContext | ImmutableUiContext> & WithNamespaces;

const EmptyUiContext = {ui: null, uiViews: List([]), parentUi: null};

const Ui = (props: UiProps) => {
  const ui = props.ctx.ui;

  const [graph] = useGraph();
  const elementsTree = useElementsTree(props.ctx.board.uid);
  const [selectedUi, setSelectedUi] = useState<Node|null>(null);
  const [newUi, setNewUi] = useState<Node|null>(null);
  const [showNewUiInput, setShowNewUiInput] = useState(false);
  const [metadata, setMetadata] = useState<UiMetadata>({});
  const [autoFocusUiConfig, setAutoFocusUiConfig] = useState(false);
  const [nodeName, setNodeName] = useState(ui? ui.getName() : '');
  const [topLevel, setTopLevel] = useState<boolean>(true);
  const [parentUi, setParentUi] = useState<Node | null>(null);
  const [viewModels, setViewModels] = useState<List<Node>>(List());

  const updateMetadata = (meta: UiMetadata) => {
    if(props.onUpdateMetadata) {
      props.onUpdateMetadata(JSON.stringify(meta, null, 2), true);
    }

    setMetadata(meta);
  }

  const updateParentUi = (changedParentUi: Node | null ) => {
    if(props.mode === 'sidebar' && selectedUi) {
      let shouldRepositionFeature = false;

      if(parentUi && !changedParentUi) {
        graph.deleteNode(parentUi);
        shouldRepositionFeature = true;
      }

      if(parentUi && changedParentUi && parentUi.getName() !== changedParentUi.getName()) {
        graph.deleteNode(parentUi);
        shouldRepositionFeature = true;
      }

      if(changedParentUi && (!parentUi || parentUi.getName() !== changedParentUi.getName())) {
        const parentUiMeta = getUiMetadata(changedParentUi);
        changedParentUi = makeNodeFromJs({
          id: graph.createCellId(),
          type: changedParentUi.getType(),
          name: changedParentUi.getName(),
          metadata: JSON.stringify(parentUiMeta, null, 2),
        });

        // Delete all VMs, so that parent Ui can be placed safely left from UI
        updateViewModels(List());

        graph.addNodeNextToAnother(changedParentUi, selectedUi, NextToPosition.left, DEFAULT_CONNECT_MARGIN);
        graph.connectNodes(changedParentUi, selectedUi);
        changedParentUi = markAsConnected(changedParentUi, graph);
        changedParentUi = graph.getNode(changedParentUi.getId());

        shouldRepositionFeature = true;

        // Redraw UI views
        updateViewModels(viewModels, updateContext(props.ctx, {parentUi: changedParentUi, uiViews: List()}));
      }

      if(shouldRepositionFeature && isDDDAction(props.ctx) && props.ctx.feature) {
        const uiOnGraph = graph.getNode(selectedUi.getId());
        if(uiOnGraph) {
          const changedFeature = repositionFeature(
            (props.ctx as ImmutableDDDWizardContext).feature!,
            // Reload elements from Graph, to get position of added ele
            dddActionFromNode(uiOnGraph, props.ctx.board),
            graph
          );
          graph.resizeNode(changedFeature, changedFeature.getSize());
        }
      }

      props.onCtxChanged(updateContext(props.ctx, {parentUi: changedParentUi}));
    }

    setParentUi(changedParentUi);
  }

  const updateViewModels = (vms: List<Node>, ctx?: ImmutableDDDWizardContext | ImmutableUiContext): void => {
    if(!ctx) {
      ctx = props.ctx;
    }

    if(props.mode === 'sidebar' && ctx.ui) {
      const shouldRepositionFeature = updateViewModelsOnGraph(ctx.ui, ctx.parentUi, ctx, vms, graph);

      // Refresh context
      const changedCtx = isDDDAction(ctx) ?
        dddActionFromNode(graph.getNode(ctx.ui!.getId())!, ctx.board)
        : uiContextFromNode(graph.getNode(ctx.ui!.getId())!, ctx.board);

      if(shouldRepositionFeature && isDDDAction(changedCtx) && changedCtx.feature) {
        const repositionedFeature = repositionFeature(
          changedCtx.feature,
          changedCtx,
          graph
        );

        graph.resizeNode(repositionedFeature, repositionedFeature.getSize());
      }

      props.onCtxChanged(changedCtx);
    }

    setViewModels(vms);
  }

  useEffect(() => {
    resetForm();

    const isTopLevel = suggestTopLevel(props.ctx.parentUi, props.ctx);
    const freshViewModels = suggestViewModels(isTopLevel, props.ctx);

    setTopLevel(isTopLevel);

    if(props.mode === 'modal') {
      setViewModels(freshViewModels);
    } else {
      setViewModels(props.ctx.uiViews);
    }

    if(ui) {
      setSelectedUi(ui);
      const meta = suggestPageConfig(getUiMetadata(ui), ui, props.ctx.parentUi, isTopLevel, freshViewModels, props.ctx);

      setMetadata(meta);
      setNodeName(ui.getName());
      setParentUi(props.ctx.parentUi);
    }
  }, [ui])

  const resetForm = () => {
    setSelectedUi(null);
    setNewUi(null);
    setShowNewUiInput(false);
    setMetadata({});
    setAutoFocusUiConfig(false);
    setNodeName('');
    setTopLevel(true);
    setParentUi(null);
  }

  const handleSubmit = (evt: FormEvent) => {
    evt.preventDefault();
    evt.stopPropagation();
  }

  const handleUiSelection = (selection: Node | null) => {
    // Also clear new UI in this case
    if(!selection) {
      resetForm();
    } else if (!selectedUi || selection.getId() !== selectedUi.getId()) {
      setSelectedUi(selection);
      const selectedMeta = suggestPageConfig(getUiMetadata(selection), selection, parentUi, topLevel, viewModels, props.ctx);

      setMetadata(selectedMeta);
    }
  }

  const handleNewUiCreated = (createdUi: Node) => {
    setShowNewUiInput(false);
    setSelectedUi(createdUi);
    setNewUi(createdUi);
    setMetadata(suggestPageConfig({}, createdUi, parentUi, topLevel, viewModels, props.ctx));
    setAutoFocusUiConfig(true);
  }

  const handleAccessibleFromChanged = (changedAccessibleFrom: AccessibleFrom) => {
    if(!changedAccessibleFrom) {
      setTopLevel(false);
      updateParentUi(null);
      updateMetadata(suggestPageConfig(metadata, selectedUi!, null, false, viewModels, props.ctx));
      return;
    }

    if(typeof changedAccessibleFrom === "string") {
      setTopLevel(true);
      updateParentUi(null);
      updateMetadata(suggestPageConfig(metadata, selectedUi!, null, true, viewModels, props.ctx));
      return;
    }

    setTopLevel(false);
    updateParentUi(changedAccessibleFrom);
    updateMetadata(suggestPageConfig(metadata, selectedUi!, changedAccessibleFrom, false, viewModels, props.ctx));
  }

  const handleViewModelsChanged = (newViewModels: List<Node>) => {
    updateViewModels(newViewModels);
  }

  const handleNameChanged = (newNodeName: string) => {
    if(ui) {
      graph.changeNodeName(ui, newNodeName);
      props.onCtxChanged(updateContext(props.ctx, {ui: ui.changeName(newNodeName)}));
    }
  }

  const handleUiDeleted = () => {
    props.onCtxChanged(updateContext(props.ctx, EmptyUiContext));
  }

  const handlePageConfigChanged = (meta: UiMetadata) => {
    updateMetadata({...metadata, ...meta});
  }

  props.onFinish((): ImmutableDDDWizardContext | ImmutableUiContext => {
    if(!selectedUi) {
      resetForm();
      return props.ctx;
    }

    let changedOrNewUi = ui
      ? makeNodeFromJs({
        ...convertNodeToJs(ui), ...{
          name: selectedUi.getName(),
          metadata: JSON.stringify(metadata, null, 2)
        }
      })
      : makeNodeFromJs({
      id: graph.createCellId(),
      type: selectedUi.getType(),
      name: selectedUi.getName(),
      metadata: JSON.stringify(metadata, null, 2),
    });

    if(!ui) {
      let isAdded = false;
      if(isDDDAction(props.ctx) && props.ctx.command) {
        graph.addNodeNextToAnother(changedOrNewUi, props.ctx.command, NextToPosition.above, DEFAULT_CONNECT_MARGIN);
        graph.connectNodes(changedOrNewUi, props.ctx.command);
        isAdded = true;
      }

      if(props.ctx.parentUi) {
        if(!isAdded) {
          graph.addNodeNextToAnother(changedOrNewUi, props.ctx.parentUi, NextToPosition.right, DEFAULT_CONNECT_MARGIN);
          isAdded = true;
        }

        graph.connectNodes(props.ctx.parentUi, changedOrNewUi);
      }

      if(!isAdded) {
        console.error("[CodyWizard] Unable to create UI. A parent UI or command card is missing to connect the UI card to.");
        return props.ctx;
      }

      changedOrNewUi = markAsConnected(changedOrNewUi, graph);
    }

    let changedOrNewParentUi = parentUi;
    let changedStateListVo: Node | undefined;

    if(!props.ctx.parentUi && parentUi) {
      const parentUiMeta = getUiMetadata(parentUi);
      changedOrNewParentUi = makeNodeFromJs({
        id: graph.createCellId(),
        type: parentUi.getType(),
        name: parentUi.getName(),
        metadata: JSON.stringify(parentUiMeta, null, 2),
      });

      graph.addNodeNextToAnother(changedOrNewParentUi, changedOrNewUi, NextToPosition.left, DEFAULT_CONNECT_MARGIN);
      graph.connectNodes(changedOrNewParentUi, changedOrNewUi);
      changedOrNewParentUi = markAsConnected(changedOrNewParentUi, graph);
      changedOrNewParentUi = graph.getNode(changedOrNewParentUi.getId());

      if(props.ctx.uiViews.count() > 0) {
        let anchorNode = changedOrNewParentUi;

        props.ctx.uiViews.forEach(vm => {
          graph.moveNodeNextToAnother(vm, anchorNode!, NextToPosition.above, DEFAULT_CONNECT_MARGIN);
          anchorNode = vm;
        })
      }

      const state = isDDDAction(props.ctx) && props.ctx.state ? props.ctx.state : viewModels.first(undefined);

      if(state && changedOrNewParentUi) {
        changedStateListVo = linkToPageFromParentUIIfPossible(changedOrNewUi, changedOrNewParentUi, state, graph, elementsTree);
      }
    }

    let shouldRepositionFeature = updateViewModelsOnGraph(changedOrNewUi, changedOrNewParentUi, props.ctx, viewModels, graph);
    let changedFeature: Node | null = null;

    if(!ui || (!props.ctx.parentUi && parentUi)) {
      shouldRepositionFeature = true;
    }

    if(!isDDDAction(props.ctx) || !props.ctx.feature) {
      shouldRepositionFeature = false;
    }

    if(shouldRepositionFeature) {
      const uiOnGraph = graph.getNode(changedOrNewUi.getId());
      if(uiOnGraph) {
        changedFeature = repositionFeature(
          (props.ctx as ImmutableDDDWizardContext).feature!,
          // Reload elements from Graph, to get position of added ele
          dddActionFromNode(uiOnGraph, props.ctx.board),
          graph
        );
        graph.resizeNode(changedFeature, changedFeature.getSize());
      }
    }

    graph.changeNodeMetadata(changedOrNewUi, changedOrNewUi.getMetadata() as NodeMetadata);

    resetForm();
    return {
      ...props.ctx,
      ui: changedOrNewUi,
      parentUi: changedOrNewParentUi,
      // Reload uiViews from graph to ensure correct positioning
      uiViews: uiContextFromNode(graph.getNode(changedOrNewUi.getId())!, props.ctx.board).uiViews,
      ...(changedStateListVo? {stateList: changedStateListVo} : {})
    };
  });

  const accessibleFrom = parentUi || (topLevel ? 'sidebar' : undefined);

  const nodeOptions = ui ? List.of(ui) : newUi ? List.of(newUi) : elementsTree
    .getFilteredElementsByType(NodeType.ui, true);

  const viewModelOptions = elementsTree.getFilteredElementsByType(NodeType.document);

  return <form className='ui form' onSubmit={handleSubmit}>
    {props.mode === 'modal' && <div className={'field'}>
      {!showNewUiInput && ui &&
          <ChangeNodeNameOrDeleteNode node={ui} board={props.ctx.board} onNameChanged={handleNameChanged}
                                      onNodeDeleted={handleUiDeleted}/>}
      {!showNewUiInput && !ui &&
          <NodeSelect nodeOptions={nodeOptions} selected={selectedUi} onChange={handleUiSelection}
                      placeholder={props.t('insp.cody_wizard.node_select.select_ui') as string}/>}
      {showNewUiInput &&
          <NewNodeInput board={props.ctx.board} nameSuggestion={suggestUiName(props.ctx, graph, topLevel)}
                        nodeType={NodeType.ui} onEntered={handleNewUiCreated}
                        onCancel={() => setShowNewUiInput(false)}/>}
      {<Button basic={true} icon="add" labelPosition="left" size="mini"
               content={props.t('insp.cody_wizard.step_ui.new_ui') as string} color="blue" className="text"
               onClick={() => setShowNewUiInput(true)}
               disabled={!!selectedUi || !!newUi || showNewUiInput}/>}
    </div>}
    <UiAccessibleFromSelect graph={graph} boardId={props.ctx.board.uid} ctx={props.ctx} accessibleFrom={accessibleFrom} ui={selectedUi} onAccessibleFromChanged={handleAccessibleFromChanged} />
    <EditorDivider content={props.t('insp.cody_wizard.step_ui.view_models_label') as string} />
    <MultipleNodeSelect
      selected={viewModels}
      placeholder={props.t('insp.cody_wizard.step_ui.select_view_models_placeholder') as string}
      nodeOptions={viewModelOptions}
      withNs={true}
      uniqueName={true}
      onChange={handleViewModelsChanged}
      disabled={!selectedUi}
    />
    <EditorDivider content={props.t('insp.cody_wizard.step_ui.page_config_label') as string} />
    <UiConfigEditor boardId={props.ctx.board.uid} uiConfig={metadata} onUiConfigChanged={handlePageConfigChanged} autofocus={autoFocusUiConfig} />
    <ButtonGroup>
      <Button basic={true} compact={true} icon="search" className="noborder" content={props.t('insp.cody_wizard.ui_schema_editor.choose_icon')} as="a" href="https://pictogrammers.com/library/mdi/" target="_blank" />
    </ButtonGroup>
  </form>

};

export default withNamespaces()(Ui);

export const updateViewModelsOnGraph = (ui: Node, parentUi: Node | null, ctx: ImmutableDDDWizardContext | ImmutableUiContext, viewModels: List<Node>, graph: Graph): boolean => {
  const existingViewModelIds = ctx.uiViews.map(vm => vm.getId());
  const changedViewModelIds = viewModels.map(vm => vm.getId());

  const viewModelsToDelete = ctx.uiViews.filter(vm => !changedViewModelIds.contains(vm.getId()));
  const viewModelsToAdd = viewModels.filter(vm => !existingViewModelIds.contains(vm.getId()));

  viewModelsToDelete.forEach(vm => graph.deleteNode(vm));

  const lastExistingViewModel = ctx.uiViews.last(undefined);
  let anchorNode = lastExistingViewModel || parentUi;
  let position = NextToPosition.above;
  if(!anchorNode) {
    anchorNode = ui;
    position = NextToPosition.left;
  }

  viewModelsToAdd.forEach(vm => {
    const newViewModel = makeNodeFromJs({
      id: graph.createCellId(),
      type: vm.getType(),
      name: vm.getName(),
      metadata: vm.getMetadata(),
    });

    graph.addNodeNextToAnother(newViewModel, anchorNode as Node, position, DEFAULT_CONNECT_MARGIN);
    graph.connectNodes(newViewModel, ui);
    markAsConnected(newViewModel, graph);
    anchorNode = graph.getNode(newViewModel.getId());
    position = NextToPosition.above;
  })

  return viewModelsToAdd.count() > 0 || viewModelsToDelete.count() > 0;
}

export const suggestPageConfig = (currentPageConfig: UiMetadata, ui: Node, parentUi: Node | null, topLevel: boolean, viewModels: List<Node>, ctx: ImmutableDDDWizardContext | ImmutableUiContext): UiMetadata => {
  const meta = {...currentPageConfig};

  if(!topLevel && (!meta.routeParams || meta.routeParams.length === 0)) {
    meta.routeParams = suggestRouteParams(ui, parentUi, topLevel, viewModels);
  }

  if(topLevel && meta.routeParams) {
    delete meta.routeParams;
  }

  if(!meta.route || meta.route === '/' || meta.route === suggestUiRoutePart(ui, topLevel, meta.routeParams || [], ctx)) {
    meta.route = suggestRoute(ui, parentUi, topLevel, meta.routeParams || [], ctx);
  }

  if(topLevel && !meta.sidebar) {
    meta.sidebar = {
      label: ui.getName(),
      icon: 'square'
    }
  }

  if(!topLevel && meta.sidebar) {
    delete meta.sidebar;
  }

  if(!meta.breadcrumb) {
    meta.breadcrumb = ui.getName()
  }

  return meta;
}

export const suggestUiRoutePart = (ui: Node | null, topLevel: boolean, routeParams: string[], ctx: ImmutableDDDWizardContext | ImmutableUiContext): string => {
  if(!ui) {
    return  "/";
  }

  if(topLevel) {
    const service = detectService(ui, ctx.board.codyEngineServiceName());
    return `/${names(service).fileName}/${names(ui.getName()).fileName}`
  }

  if(routeParams.length) {
    return "/" + routeParams.map(param => `:${param}`).join("/");
  }

  return "/";
}

export const suggestTopLevel = (parentUi: Node | null, ctx: ImmutableDDDWizardContext | ImmutableUiContext): boolean => {
  if(parentUi) {
    return false;
  } else if(isDDDAction(ctx) && ctx.command) {
    const cmdMeta = getCommandMetadata(ctx.command);
    return !!cmdMeta.newAggregate;
  }

  return true;
}

export const suggestUiName = (ctx: ImmutableDDDWizardContext | ImmutableUiContext, graph: Graph, topLevel: boolean): string | undefined => {
  let name = '';

  const suffix = topLevel ? '' : ' Details';

  if(isDDDAction(ctx)) {
    if(ctx.state) {
      name = ctx.state.getName();
    } else if(ctx.aggregate) {
      name = ctx.aggregate.getName();
    } else if(ctx.command) {
      name = ctx.command.getName();
    } else if(ctx.events.count()) {
      name = ctx.events.first(makeNodeFromJs({})).getName();
    }

    if(name === '') {
      return suffix;
    }

    const nodeNames = names(name);

    const words = tagSentence(nodeNames.title);

    console.log("[CodyWizard] tagged words: ", words);

    return words.filter(word => isNoun(word) || isAdjective(word))
      .map(word =>  isNoun(word) ? topLevel ? ensurePluralNoun(word) : ensureSingularNoun(word) : word.token).join(" ") + `${suffix}`;
  } else {
    const firstViewModel = ctx.uiViews.first(undefined);

    if(firstViewModel) {
      return firstViewModel.getName() + `${suffix}`;
    }

    if(ctx.parentUi) {
      const parentUiWords = tagSentence(ctx.parentUi.getName());
      return parentUiWords.map(word => isNoun(word)? ensureSingularNoun(word) : word.token).join(" ") + `${suffix}`;
    }
  }
}

export const suggestRouteParams = (ui: Node | null, parentUI: Node | null, topLevel: boolean, viewModels: List<Node>): string[] => {
  let routeParams: string[] = [];

  if(parentUI) {
    const parentUIMeta = getUiMetadata(parentUI);

    if(parentUIMeta.routeParams) {
      routeParams = parentUIMeta.routeParams;
    }
  }

  if(topLevel) {
    return routeParams;
  }

  viewModels.forEach(m => {
    const voMeta = getVoMetadata(m);

    if(isStateVo(m) && voMeta.identifier) {
      routeParams.push(voMeta.identifier);
    }
  })

  return routeParams;
}

export const suggestRoute = (ui: Node | null, parentUI: Node | null, topLevel: boolean, suggestedRouteParams: string[], ctx: ImmutableDDDWizardContext | ImmutableUiContext): string => {
  let route = '';

  if(parentUI) {
    const parentUIMeta = getUiMetadata(parentUI);

    if(parentUIMeta.route) {
      route +=  parentUIMeta.route;
    } else {
      route += '/' + names(parentUI.getName()).fileName;
    }
  }
  if(ui) {
    route += suggestUiRoutePart(ui, topLevel, suggestedRouteParams, ctx);
  }

  if(route === "") {
    route = suggestUiRoutePart(ui, topLevel, suggestedRouteParams, ctx);
  }

  return route;
}

export const suggestViewModels = (topLevel: boolean, ctx: ImmutableDDDWizardContext | ImmutableUiContext): List<Node> => {
  if(ctx.uiViews.count() > 0) {
    return ctx.uiViews;
  }

  if(!topLevel && isDDDAction(ctx) && ctx.state) {
    return List.of(ctx.state);
  }

  return List();
}

export const linkToPageFromParentUIIfPossible = (page: Node, parentUi: Node, state: Node, graph: Graph, elementsTree: ElementsTree): Node | undefined => {
  const stateMeta = getVoMetadata(state);
  const stateRef = getSchemaRefName(state, stateMeta);
  const pageName = names(page.getName()).className;

  const result = findStateListViewModel(stateRef, parentUi, elementsTree);

  if(!result) {
    return;
  }

  const [stateListVO, stateListVOMeta] = result;

  const uiSchema = stateListVOMeta.uiSchema || {};

  if(!uiSchema.table || !uiSchema.table.columns) {
    return;
  }

  const columns = uiSchema.table.columns;
  let hasPageLink = false;
  let voMetaChanged = false;

  columns.forEach(c => {
    if(c.pageLink && getPageNameFromPageLink(c.pageLink) === pageName) {
      hasPageLink = true;
    }
  })

  const firstColumn = columns[0];

  if(!hasPageLink && firstColumn && !firstColumn.pageLink) {
    firstColumn.pageLink = pageName;
    voMetaChanged = true;
  }

  if(voMetaChanged) {
    uiSchema.table.columns = columns;

    graph.changeNodeMetadata(stateListVO, JSON.stringify({...stateListVOMeta, uiSchema}, null, 2));

    return stateListVO;
  }
}

const getPageNameFromPageLink = (pageLink: string | PageLinkTableColumn): string => {
  if(typeof pageLink === "string") {
    return pageLink;
  }

  return pageLink.page;
}

const findStateListViewModel = (stateRef: string, parentUi: Node, elementsTree: ElementsTree): [Node, ValueObjectMetadata] | null => {
  const allCopiesOfParentUi = elementsTree.getFilteredElementsByTypeAndLabel(NodeType.ui, parentUi.getName());

  const viewModels: Node[] = [];

  allCopiesOfParentUi.forEach(copy => {
    viewModels.push(...getSourcesOfType(copy, NodeType.document))
  });

  let stateListViewModel: Node | null = null;
  let stateListViewModelMeta: ValueObjectMetadata | null = null;

  viewModels.forEach(vm => {
    if(isListVo(vm)) {
      const listVoMeta = getVoMetadata(vm);
      const itemsSchema = listVoMeta.schema!.getListItemsSchema(new Schema({}));

      if(itemsSchema.isRef() && itemsSchema.getRef() === stateRef) {
        stateListViewModel = vm;
        stateListViewModelMeta = listVoMeta;
      }
    }
  })

  if(stateListViewModel && stateListViewModelMeta) {
    return [stateListViewModel, stateListViewModelMeta];
  }

  return null;
}
