import {JSONSchema7} from "json-schema";
import {
  makeAssignVariable, makeCallService,
  makeDefinitions, makeExecuteRules,
  makeForEach,
  makeThenRecordEvent,
  makeThenThrowError, makeThenTriggerCommand
} from "./rule-engine-definitions";

export const makePolicyRulesSchema = (possibleCommands: string[], possibleExternalSystems: string[]): string => {
  const PolicyRulesSchema: JSONSchema7 = {
    type: "array",
    items: {
      $ref: "#/definitions/rule_definition"
    },
    definitions: makeDefinitions(
      makeThenTriggerCommand(possibleCommands),
      makeCallService(possibleExternalSystems),
      makeThenThrowError(),
      makeAssignVariable(),
      makeForEach(),
      makeExecuteRules()
    )
  }

  return JSON.stringify(PolicyRulesSchema);
}
