import * as React from 'react';
import EditableTextline from "../../../../core/components/EditableTextline";
import {useGraph} from "../../../hooks/useGraph";
import {makeNodeFromJs, Node, NodeType} from "../../../model/Graph";
import {Board} from "../../../model/Board";
import {useState} from "react";
import {useElementsTree} from "../../../hooks/useElementsTree";
import {withNamespaces, WithNamespaces} from "react-i18next";

interface OwnProps {
  nameSuggestion?: string;
  nodeType: NodeType;
  board: Board;
  onEntered: (newNode: Node) => void;
  onCancel: () => void;
  uniqueName?: boolean;
}

type NewNodeInputProps = OwnProps & WithNamespaces;

const NewNodeInput = (props: NewNodeInputProps) => {

  const [graph,] = useGraph();
  const elementsTree = useElementsTree(props.board.uid);

  const handleTextChanged = (name: string) => {
    const newNode = makeNodeFromJs({
      id: graph.createCellId(),
      name: name.trim(),
      type: props.nodeType
    });

    props.onEntered(newNode);
  }

  const validate = (name: string): boolean | string => {
    if(props.uniqueName) {
      const newNode = makeNodeFromJs({
        id: graph.createCellId(),
        name: name.trim(),
        type: props.nodeType
      });

      const unique = elementsTree.getSimilarElements(newNode).count() === 0;

      if(!unique) {
        return props.t('insp.cody_wizard.node_select.unique_name_error') as string;
      }
    }

    return true;
  }

  return <EditableTextline
    text={''}
    textSuggestion={props.nameSuggestion || ''}
    required={true}
    charSize={1}
    fixedWidth={true}
    widthUnit={'%'}
    minWidth={100}
    onTextChanged={handleTextChanged}
    focus={true}
    onCancel={props.onCancel}
    validate={validate}
  />
};

export default withNamespaces()(NewNodeInput);
