import {Node} from "../../../model/Graph";
import {parseNodeMetadata} from "../node/metadata";
import {Rule, ThenAssignVariable} from "../rule-engine/configuration";
import {Schema} from "../schema/schema";

export interface ApplyEventRule extends Rule {
  then: ThenAssignVariable
}

export interface AggregateEventMetadata {
  public?: boolean;
  shorthand?: boolean;
  schema?: Schema;
  applyRules?: ApplyEventRule[];
}

export const getAggregateEventMetadata = (node: Node): AggregateEventMetadata => {
  const metadata = parseNodeMetadata(node);

  metadata.public = false;

  if(metadata.schema) {
    metadata.schema = new Schema(metadata.schema);
  }

  // @TODO: validate rules

  return metadata;
}
