import {List} from "immutable";
import * as React from 'react';
import {FormEvent, useEffect, useState} from 'react';
import {withNamespaces, WithNamespaces} from "react-i18next";
import {Button} from "semantic-ui-react";
import {useElementsTree} from "../../../hooks/useElementsTree";
import {useGraph} from "../../../hooks/useGraph";
import {
  convertNodeToJs,
  Graph,
  makeNodeFromJs,
  NextToPosition,
  Node,
  NodeMetadata,
  NodeType
} from "../../../model/Graph";
import {ImmutablePolicyContext, policyContextFromNode} from "../../../service/cody-wizard/context/policy-context";
import {updateContext} from "../../../service/cody-wizard/context/wizard-context";
import {getAggregateEventMetadata} from "../../../service/cody-wizard/event/get-aggregate-event-metadata";
import {fqcn} from "../../../service/cody-wizard/node/fqcn";
import {markAsConnected} from "../../../service/cody-wizard/node/mark-as-connected";
import {markAsDisconnected} from "../../../service/cody-wizard/node/mark-as-disconnected";
import {getPolicyMetadata, PolicyMetadata} from "../../../service/cody-wizard/policy/get-policy-metadata";
import {
  isExecuteRules, isForEach,
  isIfConditionRule, isIfNotConditionRule,
  isTriggerCommand,
  Rule, ThenType
} from "../../../service/cody-wizard/rule-engine/configuration";
import {
  convertDependenciesToMetadataConfig
} from "../../../service/cody-wizard/rule-engine/convert-dependencies-to-metadata-config";
import {getDependencyName} from "../../../service/cody-wizard/rule-engine/get-dependency-name";
import {suggestRulesDependencyConfig} from "../../../service/cody-wizard/rule-engine/suggest-rules-dependency-config";
import {RulesDependency} from "../../../service/cody-wizard/rule-engine/types";
import {Schema} from "../../../service/cody-wizard/schema/schema";
import DependenciesEditor from "../Editor/DependenciesEditor";
import EditorDivider from "../Editor/EditorDivider";
import PolicyRulesEditor from "../Editor/PolicyRulesEditor";
import ChangeNodeNameOrDeleteNode from "../Node/ChangeNodeNameOrDeleteNode";
import MultipleNodeSelect from "../Node/MultipleNodeSelect";
import NewNodeInput from "../Node/NewNodeInput";
import NodeSelect from "../Node/NodeSelect";
import {DEFAULT_CONNECT_MARGIN, WithWizardStepProps} from "../WizardModal";
import {repositionFeature} from "./Feature";
import {AggregateMetadata} from "../../../service/cody-wizard/aggregate/get-aggregate-metadata";
import {dddActionFromNode} from "../../../service/cody-wizard/context/ddd-action";
import {getCommandMetadata} from "../../../service/cody-wizard/command/get-command-metadata";

interface OwnProps {

}

type PolicyProps = OwnProps & WithWizardStepProps<ImmutablePolicyContext> & WithNamespaces;

const Policy = (props: PolicyProps) => {
  const policy = props.ctx.policy;

  const [graph] = useGraph();
  const elementsTree = useElementsTree(props.ctx.board.uid);
  const [selectedPolicy, setSelectedPolicy] = useState<Node|null>(null);
  const [newPolicy, setNewPolicy] = useState<Node|null>(null);
  const [showNewPolicyInput, setShowNewPolicyInput] = useState(false);
  const [metadata, setMetadata] = useState<PolicyMetadata>({});
  const [autoFocusPolicyRules, setAutoFocusPolicyRules] = useState(false);
  const [dependencies, setDependencies] = useState<Record<string, RulesDependency>>({});
  const [showDependencies, setShowDependencies] = useState(false);
  const [policyTargets, setPolicyTargets] = useState(props.ctx.policyTargets);

  const updateMetadata = (meta: PolicyMetadata) => {
    if(props.onUpdateMetadata) {
      props.onUpdateMetadata(JSON.stringify(meta, null, 2), true);
    }

    setMetadata(meta);
  }

  const updatePolicyTargets = (targets: List<Node>) => {
    if(props.mode === "sidebar" && selectedPolicy) {
      const shouldRepositionFeature = updateTargetsOnGraph(selectedPolicy, props.ctx, targets, graph);
      // Refresh context
      const changedCtx = policyContextFromNode(graph.getNode(selectedPolicy.getId())!, props.ctx.board);

      if(shouldRepositionFeature && props.ctx.feature) {
        const repositionedFeature = repositionFeature(
          props.ctx.feature,
          policyContextFromNode(graph.getNode(selectedPolicy.getId())!, props.ctx.board),
          graph
        );

        graph.resizeNode(repositionedFeature, repositionedFeature.getSize());
      }

      props.onCtxChanged(changedCtx);

    }

    setPolicyTargets(targets);
  }

  const updateDependencies = (deps: Record<string, RulesDependency>) => {
    if(props.mode === "sidebar" && selectedPolicy) {
      const shouldRepositionFeature = updateDependenciesOnGraph(selectedPolicy, props.ctx, List(Object.values(deps).map(dep => dep.node)), graph);
      // Refresh context
      const changedCtx = policyContextFromNode(graph.getNode(selectedPolicy.getId())!, props.ctx.board);

      if(shouldRepositionFeature && props.ctx.feature) {
        const repositionedFeature = repositionFeature(
          props.ctx.feature,
          policyContextFromNode(graph.getNode(selectedPolicy.getId())!, props.ctx.board),
          graph
        );

        graph.resizeNode(repositionedFeature, repositionedFeature.getSize());
      }

      props.onCtxChanged(changedCtx);

    }

    setDependencies(deps);
  }

  useEffect(() => {
    resetForm();

    if(policy) {
      setSelectedPolicy(policy);
      const meta = getPolicyMetadata(policy);
      setMetadata(meta);
    } else {
      setShowNewPolicyInput(true);
    }

    if(Object.keys(props.ctx.policyDependencies).length) {
      setShowDependencies(true);
    }

    setDependencies(props.ctx.policyDependencies);
    setPolicyTargets(props.ctx.policyTargets);
  }, [policy])

  const resetForm = () => {
    setSelectedPolicy(null);
    setNewPolicy(null);
    setShowNewPolicyInput(false);
    setMetadata({});
    setAutoFocusPolicyRules(false);
    setDependencies({});
    setShowDependencies(false);
    setPolicyTargets(List([]));
  }

  const handleSubmit = (evt: FormEvent) => {
    evt.preventDefault();
    evt.stopPropagation();
  }

  const handlePolicySelection = (selection: Node | null) => {
    // Also clear new policy in this case
    if(!selection) {
      resetForm();
    } else if (!selectedPolicy || selection.getId() !== selectedPolicy.getId()) {
      setSelectedPolicy(selection);
      const selectedMeta = getPolicyMetadata(selection);

      // Always reset rules
      selectedMeta.rules = suggestTriggerRules(policyTargets, metadata, props.ctx);

      setMetadata(selectedMeta);
    }
  }

  const handleNewPolicyCreated = (createdPolicy: Node) => {
    setShowNewPolicyInput(false);
    setSelectedPolicy(createdPolicy);
    setNewPolicy(createdPolicy);
    setMetadata({rules: suggestTriggerRules(policyTargets, metadata, props.ctx)});
    setAutoFocusPolicyRules(true);
  }

  const handleNameChanged = (newNodeName: string) => {
    if(policy) {
      graph.changeNodeName(policy, newNodeName);
      props.onCtxChanged(updateContext(props.ctx, {policy: policy.changeName(newNodeName)}));
    }
  }

  const handlePolicyDeleted = () => {
    if(props.ctx.policyDependencies) {
      Object.values(props.ctx.policyDependencies).forEach(dep => {
        graph.deleteNode(dep.node);
      })
    }

    props.onCtxChanged(updateContext(props.ctx, {policy: null, policyDependencies: {}}));
  }

  const handleRulesChanged = (newRuleSet: Rule[]) => {
    updateMetadata({...metadata, rules: newRuleSet});
  }

  const handleDependenciesChanged = (changedDependencies: List<Node>) => {
    const newDependencies: Record<string, RulesDependency> = {};

    changedDependencies.forEach(dep => {
      const depName = getDependencyName(dep, props.ctx);
      if(!dependencies[depName]) {
        newDependencies[depName] = suggestRulesDependencyConfig(dep, props.ctx);
      } else {
        newDependencies[depName] = dependencies[depName];
      }
    })

    updateDependencies(newDependencies);
    updateMetadata({...metadata, dependencies: convertDependenciesToMetadataConfig(newDependencies)});
  }

  const handleDependenciesConfigChanged = (changedDependencies: Record<string, RulesDependency>) => {
    updateDependencies(changedDependencies);
    updateMetadata({...metadata, dependencies: convertDependenciesToMetadataConfig(changedDependencies)});
  }

  const handlePolicyTargetsChanged = (changedPolicyTargets: List<Node>) => {
    updatePolicyTargets(changedPolicyTargets);
    handleRulesChanged(suggestTriggerRules(changedPolicyTargets, metadata, props.ctx));
  }

  props.onFinish((): ImmutablePolicyContext => {
    console.log("[CodyWizard] finish policy");
    if(!selectedPolicy) {
      resetForm();
      return props.ctx;
    }

    let changedOrNewPolicy = policy
      ? makeNodeFromJs({
        ...convertNodeToJs(policy), ...{
          name: selectedPolicy.getName(),
          metadata: JSON.stringify(metadata, null, 2)
        }
      })
      : makeNodeFromJs({
        id: graph.createCellId(),
        type: selectedPolicy.getType(),
        name: selectedPolicy.getName(),
        metadata: JSON.stringify(metadata, null, 2),
      });

    if(!policy) {
      let isAdded = false;
      if(props.ctx.event) {
        graph.addNodeNextToAnother(changedOrNewPolicy, props.ctx.event, NextToPosition.right, DEFAULT_CONNECT_MARGIN);
        graph.connectNodes(props.ctx.event, changedOrNewPolicy);
        isAdded = true;
      }

      if(props.ctx.policyTargets.count()) {
        if(!isAdded) {
          const firstTarget = props.ctx.policyTargets.first(undefined);
          if(firstTarget) {
            graph.addNodeNextToAnother(changedOrNewPolicy, firstTarget, NextToPosition.left, DEFAULT_CONNECT_MARGIN);
            isAdded = true;
            graph.connectNodes(changedOrNewPolicy, firstTarget);
          }
        }
      }

      if(!isAdded) {
        console.error("[CodyWizard] Unable to create policy. A command, externalSystem or event card is missing to connect the policy card to.");
        return props.ctx;
      }

      // Explicitly mark policy as disconnected, since each policy card defines its own processing rules
      changedOrNewPolicy = markAsDisconnected(changedOrNewPolicy, graph);
    }

    const depsUpdated = updateDependenciesOnGraph(changedOrNewPolicy, props.ctx, List(Object.values(dependencies).map(dep => dep.node)), graph);
    const targetsUpdated = updateTargetsOnGraph(changedOrNewPolicy, props.ctx, policyTargets, graph);

    if((depsUpdated || targetsUpdated || !policy) && props.ctx.feature) {
      const repositionedFeature = repositionFeature(
        props.ctx.feature,
        policyContextFromNode(graph.getNode(changedOrNewPolicy.getId())!, props.ctx.board),
        graph
      )

      graph.resizeNode(repositionedFeature, repositionedFeature.getSize());
    }

    graph.changeNodeMetadata(changedOrNewPolicy, changedOrNewPolicy.getMetadata() as NodeMetadata);

    resetForm();
    return {...props.ctx, policy: changedOrNewPolicy, policyDependencies: dependencies};
  });

  const nodeOptions = policy ? List.of(policy) : newPolicy ? List.of(newPolicy) : List([]);

  const depsOptions = elementsTree.getFilteredElementsByTypes([NodeType.document, NodeType.externalSystem]);
  const targetOptions = elementsTree.getFilteredElementsByTypes([NodeType.command, NodeType.externalSystem]);
  const eventSchema = props.ctx.event ? getAggregateEventMetadata(props.ctx.event).schema || new Schema({}) : new Schema({});

  return <form className='ui form' onSubmit={handleSubmit}>
    {props.mode === 'modal' && <div className={'field'}>
      {!showNewPolicyInput && policy &&
          <ChangeNodeNameOrDeleteNode node={policy} board={props.ctx.board} uniqueName={true}
                                      onNameChanged={handleNameChanged} onNodeDeleted={handlePolicyDeleted}/>}
      {!showNewPolicyInput && !policy &&
          <NodeSelect nodeOptions={nodeOptions} selected={selectedPolicy} onChange={handlePolicySelection}
                      placeholder={props.t('insp.cody_wizard.node_select.select_policy') as string}/>}
      {showNewPolicyInput &&
          <NewNodeInput board={props.ctx.board} uniqueName={true} nameSuggestion={suggestPolicyName(props.ctx)}
                        nodeType={NodeType.policy} onEntered={handleNewPolicyCreated}
                        onCancel={() => setShowNewPolicyInput(false)}/>}
      {<Button basic={true} icon="add" labelPosition="left" size="mini"
               content={props.t('insp.cody_wizard.step_policy.new_policy') as string} color="blue" className="text"
               onClick={() => setShowNewPolicyInput(true)}
               disabled={!!selectedPolicy || !!newPolicy || showNewPolicyInput}/>}
    </div>}
    <div className={'field'}>
      {<Button basic={true} icon={showDependencies? 'minus' : 'add'} labelPosition="left" size="mini"
               content={props.t('insp.cody_wizard.step_policy.manage_dependencies') + ` (${Object.keys(dependencies).length})`} color="blue" className="text"
               onClick={() => setShowDependencies(!showDependencies)}
               disabled={!selectedPolicy}/>}
    </div>
    <div className={(showDependencies? 'slidedown' : 'slideup') + ' smooth'} >
      <EditorDivider content={props.t('insp.cody_wizard.step_policy.dependencies_label') as string} />
      <MultipleNodeSelect selected={List(Object.values(dependencies).map(dep => dep.node))}
                          nodeOptions={depsOptions}
                          placeholder={props.t('insp.cody_wizard.step_policy.dependencies_placeholder') as string}
                          withNs={true}
                          uniqueName={true}
                          onChange={handleDependenciesChanged}
                          disabled={!selectedPolicy} />
      <DependenciesEditor boardId={props.ctx.board.uid} dependencies={dependencies} messageSchema={eventSchema} autofocus={false} onDependenciesChanged={handleDependenciesConfigChanged} />

    </div>
    <EditorDivider content={props.t('insp.cody_wizard.step_policy.rules_label') as string} />
    <MultipleNodeSelect selected={policyTargets}
                        nodeOptions={targetOptions}
                        placeholder={props.t('insp.cody_wizard.step_policy.targets_placeholder') as string}
                        uniqueName={true}
                        onChange={handlePolicyTargetsChanged}
                        disabled={!selectedPolicy} />
    <PolicyRulesEditor rules={metadata.rules || suggestTriggerRules(policyTargets, metadata, props.ctx)}
                       policyTargets={policyTargets}
                       dependencies={dependencies}
                       onRulesChanged={handleRulesChanged}
                       autofocus={autoFocusPolicyRules}
                       ctx={props.ctx}
    />
  </form>
};

export default withNamespaces()(Policy);

export const suggestPolicyName = (ctx: ImmutablePolicyContext): string => {
  let name = 'On ';

  if(ctx.event) {
    name += ctx.event.getName();
  }

  return name;
}


export const suggestTriggerRules = (policyTargets: List<Node>, metadata: PolicyMetadata, ctx: ImmutablePolicyContext): Rule[] => {
  const rules: Rule[] = metadata.rules ? [...metadata.rules] : [];

  policyTargets.forEach(t => {
    if(t.getType() === NodeType.command) {
      const cmdFQCN = fqcn(t, ctx.board.codyEngineServiceName());
      if(!hasTriggerCommandRule(cmdFQCN, rules)) {
        rules.push({
          rule: "always",
          then: {
            trigger: {
              command: fqcn(t, ctx.board.codyEngineServiceName()),
              mapping: "event"
            }
          }
        })
      }
    }
  })

  return rules;
}

const hasTriggerCommandRule = (command: string, rules: Rule[]): boolean => {
  for (const rule of rules) {
    if(isThenTriggerCommand(command, rule.then)) {
      return true
    }

    if(isExecuteRules(rule.then) && hasTriggerCommandRule(command, rule.then.execute.rules)) {
      return true;
    }

    if(isIfConditionRule(rule) || isIfNotConditionRule(rule)) {
      if(rule.else && isThenTriggerCommand(command, rule.else)) {
        return true;
      }
    }
  }

  return false;
}

const isThenTriggerCommand = (command: string, then: ThenType): boolean => {
  if(isTriggerCommand(then) && then.trigger.command === command) {
    return true
  }

  if(isForEach(then) && isThenTriggerCommand(command, then.forEach.then)) {
    return true;
  }

  return false;
}

const updateDependenciesOnGraph = (policy: Node, ctx: ImmutablePolicyContext, dependencies: List<Node>, graph: Graph): boolean => {
  const existingDependencies = List(Object.values(ctx.policyDependencies || {}).map(dep => dep.node));
  const existingDependencyIds = existingDependencies.map(dep => dep.getId());
  const changedDependencyIds = dependencies.map(dep => dep.getId());

  const dependenciesToDelete = existingDependencies.filter(dep => !changedDependencyIds.contains(dep.getId()));
  const dependenciesToAdd = dependencies.filter(dep => !existingDependencyIds.contains(dep.getId()));

  dependenciesToDelete.forEach(dep => graph.deleteNode(dep));

  const lastExistingDependency = existingDependencies.last(undefined);
  let anchorNode = lastExistingDependency || ctx.event;
  let position = NextToPosition.above;
  if(!anchorNode) {
    anchorNode = policy;
    position = NextToPosition.left;
  }

  dependenciesToAdd.forEach(dep => {
    const newDependency = makeNodeFromJs({
      id: graph.createCellId(),
      type: dep.getType(),
      name: dep.getName(),
      metadata: dep.getMetadata(),
    });

    graph.addNodeNextToAnother(newDependency, anchorNode as Node, position, DEFAULT_CONNECT_MARGIN);
    graph.connectNodes(newDependency, policy);
    markAsConnected(newDependency, graph);
    anchorNode = graph.getNode(newDependency.getId());
    position = NextToPosition.above;
  })

  return dependenciesToAdd.count() > 0 || dependenciesToDelete.count() > 0;
}

const updateTargetsOnGraph = (policy: Node, ctx: ImmutablePolicyContext, targets: List<Node>, graph: Graph): boolean => {
  const existingTargets = ctx.policyTargets;
  const existingTargetIds = existingTargets.map(dep => dep.getId());
  const changedTargetIds = targets.map(dep => dep.getId());

  const targetsToDelete = existingTargets.filter(dep => !changedTargetIds.contains(dep.getId()));
  const targetsToAdd = targets.filter(dep => !existingTargetIds.contains(dep.getId()));

  targetsToDelete.forEach(t => graph.deleteNode(t));

  const lastExistingTarget = existingTargets.last(undefined);
  let anchorNode = lastExistingTarget || null;
  let position = NextToPosition.below;
  if(!anchorNode) {
    anchorNode = policy;
    position = NextToPosition.right;
  }

  targetsToAdd.forEach(t => {
    const newTarget = makeNodeFromJs({
      id: graph.createCellId(),
      type: t.getType(),
      name: t.getName(),
      metadata: t.getMetadata(),
    });

    graph.addNodeNextToAnother(newTarget, anchorNode as Node, position, DEFAULT_CONNECT_MARGIN);
    graph.connectNodes(policy, newTarget);
    markAsConnected(newTarget, graph);
    anchorNode = graph.getNode(newTarget.getId());
    position = NextToPosition.below;
  })

  return targetsToAdd.count() > 0 || targetsToDelete.count() > 0;
}
