import {List} from "immutable";
import * as React from 'react';
import {FormEvent, useEffect, useState} from 'react';
import {withNamespaces, WithNamespaces} from "react-i18next";
import {Button} from "semantic-ui-react";
import {useElementsTree} from "../../../hooks/useElementsTree";
import {useGraph} from "../../../hooks/useGraph";
import {
  convertNodeToJs,
  Graph,
  makeNodeFromJs,
  NextToPosition,
  Node,
  NodeMetadata,
  NodeType
} from "../../../model/Graph";
import {getCommandMetadata} from "../../../service/cody-wizard/command/get-command-metadata";
import {dddActionFromNode, ImmutableDDDWizardContext} from "../../../service/cody-wizard/context/ddd-action";
import {updateContext} from "../../../service/cody-wizard/context/wizard-context";
import {
  AggregateEventMetadata, ApplyEventRule,
  getAggregateEventMetadata
} from "../../../service/cody-wizard/event/get-aggregate-event-metadata";
import {isAggregateEventCandidate} from "../../../service/cody-wizard/event/is-aggregate-event";
import {markAsConnected} from "../../../service/cody-wizard/node/mark-as-connected";
import {Schema} from "../../../service/cody-wizard/schema/schema";
import {getVoMetadata} from "../../../service/cody-wizard/vo/get-vo-metadata";
import EditorDivider from "../Editor/EditorDivider";
import EventApplyRulesEditor from "../Editor/EventApplyRulesEditor";
import SchemaEditor from "../Editor/SchemaEditor";
import ChangeNodeNameOrDeleteNode from "../Node/ChangeNodeNameOrDeleteNode";
import NewNodeInput from "../Node/NewNodeInput";
import NodeSelect from "../Node/NodeSelect";
import {DEFAULT_CONNECT_MARGIN, WithWizardStepProps} from "../WizardModal";
import {repositionFeature} from "./Feature";

interface OwnProps {

}

type AggregateEventProps = OwnProps & WithWizardStepProps<ImmutableDDDWizardContext> & WithNamespaces;

const AggregateEvent = (props: AggregateEventProps) => {
  const event = props.ctx.events.first(null);

  const [graph] = useGraph();
  const elementsTree = useElementsTree(props.ctx.board.uid);
  const [selectedEvent, setSelectedEvent] = useState<Node|null>(null);
  const [newEvent, setNewEvent] = useState<Node|null>(null);
  const [showNewEventInput, setShowNewEventInput] = useState(false);
  const [metadata, setMetadata] = useState<AggregateEventMetadata>({});
  const [autoFocusSchema, setAutoFocusSchema] = useState(false);
  const [nodeName, setNodeName] = useState(event? event.getName() : '');

  const updateMetadata = (meta: AggregateEventMetadata) => {
    if(props.onUpdateMetadata) {
      props.onUpdateMetadata(JSON.stringify(meta, null, 2), true);
    }

    setMetadata(meta);
  }

  useEffect(() => {
    resetForm();

    if(event) {
      setSelectedEvent(event);
      const meta = getAggregateEventMetadata(event);

      let updateMeta = false;

      if(!meta.schema || meta.schema.isEmpty()) {
        meta.schema = suggestEventSchema(props.ctx);
        updateMeta = true;
      }

      if(!meta.applyRules) {
        meta.applyRules = [defaultApplyRule()];
        updateMeta = true;
      }

      if(updateMeta) {
        updateMetadata(meta);
      } else {
        setMetadata(meta);
      }
      setNodeName(event.getName());
    }

  }, [event])

  const resetForm = () => {
    setSelectedEvent(null);
    setNewEvent(null);
    setShowNewEventInput(false);
    setMetadata({});
    setAutoFocusSchema(false);
    setNodeName('');
  }

  const handleSubmit = (evt: FormEvent) => {
    evt.preventDefault();
    evt.stopPropagation();
  }

  const handleEventSelection = (selection: Node | null) => {
    // Also clear new event in this case
    if(!selection) {
      resetForm();
    } else if (!selectedEvent || selection.getId() !== selectedEvent.getId()) {
      setSelectedEvent(selection);
      const selectedMeta = getAggregateEventMetadata(selection);

      if(!selectedMeta.schema || selectedMeta.schema.isEmpty()) {
        selectedMeta.schema = suggestEventSchema(props.ctx);
      }

      setMetadata(selectedMeta);
    }
  }

  const handleNewEventCreated = (createdEvent: Node) => {
    const schema = suggestEventSchema(props.ctx);
    setShowNewEventInput(false);
    setSelectedEvent(createdEvent);
    setNewEvent(createdEvent);
    setMetadata({
      schema,
      applyRules: []
    });
    setAutoFocusSchema(true);
  }

  const handleNameChanged = (newNodeName: string) => {
    if(event) {
      graph.changeNodeName(event, newNodeName);
      props.onCtxChanged(updateContext(props.ctx, {events: List.of(event.changeName(newNodeName))}));
    }
  }

  const handleEventDeleted = () => {
    props.onCtxChanged(updateContext(props.ctx, {events: List([])}));
  }

  const handleSchemaChanged = (newSchema: Schema) => {
    updateMetadata({...metadata, schema: newSchema});
  }

  const handleCopyStateSchema = () => {
    if(props.ctx.state) {
      const stateMeta = getVoMetadata(props.ctx.state);

      if(stateMeta.schema) {
        handleSchemaChanged(stateMeta.schema);
        return;
      }
    }

    handleSchemaChanged({} as Schema);
  }

  const handleEventApplyRulesChanged = (changedRules: ApplyEventRule[]) => {
    updateMetadata({...metadata, applyRules: changedRules});
  }

  props.onFinish((): ImmutableDDDWizardContext => {
    if(!selectedEvent) {
      resetForm();
      return props.ctx;
    }

    let changedOrNewEvent = event
      ? makeNodeFromJs({...convertNodeToJs(event), ...{name: selectedEvent!.getName(), metadata: JSON.stringify(metadata, null, 2)}})
      : makeNodeFromJs({
      id: graph.createCellId(),
      type: selectedEvent.getType(),
      name: selectedEvent.getName(),
      metadata: JSON.stringify(metadata, null, 2),
    });

    if(!event) {
      let isAdded = false;
      if(props.ctx.aggregate) {
        graph.addNodeNextToAnother(changedOrNewEvent, props.ctx.aggregate, NextToPosition.below, DEFAULT_CONNECT_MARGIN);
        graph.connectNodes(props.ctx.aggregate, changedOrNewEvent);
        isAdded = true;
      }

      if(props.ctx.state) {
        if(!isAdded) {
          graph.addNodeNextToAnother(changedOrNewEvent, props.ctx.state, NextToPosition.above, DEFAULT_CONNECT_MARGIN);
          isAdded = true;
        }

        graph.connectNodes(changedOrNewEvent, props.ctx.state);
      }

      if(!isAdded) {
        console.error("[CodyWizard] Unable to create Event. An aggregate or state card is missing to connect the event card to.");
        return props.ctx;
      }

      changedOrNewEvent = markAsConnected(changedOrNewEvent, graph);

      if(props.ctx.feature) {
        const repositionedFeature = repositionFeature(
          props.ctx.feature,
          dddActionFromNode(graph.getNode(changedOrNewEvent.getId())!, props.ctx.board),
          graph
        )

        graph.resizeNode(repositionedFeature, repositionedFeature.getSize());
      }
    }

    graph.changeNodeMetadata(changedOrNewEvent, changedOrNewEvent.getMetadata() as NodeMetadata);

    const existingEvents = props.ctx.events.filter(ee => ee.getId() !== changedOrNewEvent.getId());

    resetForm();
    return {...props.ctx, events: existingEvents.push(changedOrNewEvent)};
  });

  const nodeOptions = event ? List.of(event) : newEvent ? List.of(newEvent) : elementsTree
    .getFilteredElementsByType(NodeType.event, true).filter(candidate => isAggregateEventCandidate(candidate));

  return <form className='ui form' onSubmit={handleSubmit}>
    {props.mode === 'modal' && <div className={'field'}>
      {!showNewEventInput && event &&
          <ChangeNodeNameOrDeleteNode node={event} board={props.ctx.board} onNameChanged={handleNameChanged}
                                      onNodeDeleted={handleEventDeleted}/>}
      {!showNewEventInput && !event &&
          <NodeSelect nodeOptions={nodeOptions} selected={selectedEvent} onChange={handleEventSelection}
                      placeholder={props.t('insp.cody_wizard.node_select.select_aggregate_event') as string}/>}
      {showNewEventInput &&
          <NewNodeInput board={props.ctx.board} nameSuggestion={suggestAggregateEventName(props.ctx, graph)}
                        nodeType={NodeType.event} onEntered={handleNewEventCreated}
                        onCancel={() => setShowNewEventInput(false)}/>}
      {<Button basic={true} icon="add" labelPosition="left" size="mini"
               content={props.t('insp.cody_wizard.step_aggregate_event.new_event') as string} color="blue"
               className="text"
               onClick={() => setShowNewEventInput(true)}
               disabled={!!selectedEvent || !!newEvent || showNewEventInput}/>}
    </div>}
    <EditorDivider content="Schema" />
    <SchemaEditor boardId={props.ctx.board.uid} schema={metadata.schema || Schema.fromString('{}')} onSchemaChanged={handleSchemaChanged} autofocus={autoFocusSchema} />
    <Button basic={true} compact={true} icon="copy" className="noborder" content={props.t('insp.cody_wizard.schema_editor.copy_state_schema')} disabled={!props.ctx.state} onClick={handleCopyStateSchema} />
    <EditorDivider content="Information Update" />
    <EventApplyRulesEditor ctx={props.ctx} applyRules={metadata.applyRules || [defaultApplyRule()]} eventSchema={metadata.schema || new Schema({})} informationSchema={getStateSchema(props.ctx)} onRulesChanged={handleEventApplyRulesChanged} />
  </form>
};

export default withNamespaces()(AggregateEvent);

export const suggestEventSchema = (ctx: ImmutableDDDWizardContext): Schema => {
  if(ctx.command) {
    const cmdMetadata = getCommandMetadata(ctx.command);

    if(cmdMetadata.schema) {
      return cmdMetadata.schema;
    }
  }

  if(ctx.state) {
    const stateMetadata = getVoMetadata(ctx.state);

    if(stateMetadata.schema) {
      return stateMetadata.schema;
    }
  }

  return new Schema({});
}

export const suggestAggregateEventName = (ctx: ImmutableDDDWizardContext, graph: Graph): string => {
  // @TODO: suggest from command name or state/aggregate name + Added/Updated
  if(ctx.state) {
    return ctx.state.getName() + ' Changed';
  }

  if(ctx.aggregate) {
    return ctx.aggregate.getName() + ' Changed';
  }

  return '';
}

export const defaultApplyRule = (): ApplyEventRule => {
  return {
    rule: "always",
    then: {
      assign: {
        variable: "information",
        value: {
          $merge: ["information", "event"]
        }
      }
    }
  }
}

const getStateSchema = (ctx: ImmutableDDDWizardContext): Schema => {
  if(!ctx.state) {
    return new Schema({});
  }

  const meta = getVoMetadata(ctx.state);

  return meta.schema || new Schema({});
}
