import {List} from "immutable";
import * as React from 'react';
import {FormEvent, useEffect, useState} from "react";
import {withNamespaces, WithNamespaces} from "react-i18next";
import {Button} from "semantic-ui-react";
import {useElementsTree} from "../../../hooks/useElementsTree";
import {useGraph} from "../../../hooks/useGraph";
import {
  convertNodeToJs, Graph,
  GraphPoint,
  makeNodeFromJs,
  Node,
  NodeSize,
  NodeType
} from "../../../model/Graph";
import {ImmutableDDDWizardContext, isDDDAction} from "../../../service/cody-wizard/context/ddd-action";
import {ImmutablePolicyContext, isPolicyContext} from "../../../service/cody-wizard/context/policy-context";
import {updateContext, WizardContext} from "../../../service/cody-wizard/context/wizard-context";
import EditorDivider from "../Editor/EditorDivider";
import ChangeNodeNameOrDeleteNode from "../Node/ChangeNodeNameOrDeleteNode";
import NewNodeInput from "../Node/NewNodeInput";
import NodeSelect from "../Node/NodeSelect";
import {WithWizardStepProps} from "../WizardModal";
import {suggestNewCommandName} from "./AggregateCommand";
import StatusSelect from "./Feature/StatusSelect";
import TaskLinkInput from "./Feature/TaskLinkInput";
import {ImmutableFeatureContext} from "../../../service/cody-wizard/context/feature-context";
import {ValueObjectMetadata} from "../../../service/cody-wizard/vo/get-vo-metadata";

interface OwnProps {

}

export type FeatureStatus = 'important' | 'planned' | 'ready' | 'deployed';
export const POSSIBLE_STATUS = ['important', 'planned', 'ready', 'deployed'];

type FeatureProps = OwnProps & WithWizardStepProps<ImmutableDDDWizardContext | ImmutablePolicyContext | ImmutableFeatureContext> & WithNamespaces;

const Feature = (props: FeatureProps) => {
  const [graph] = useGraph();
  const elementsTree = useElementsTree(props.ctx.board.uid);
  const [selectedFeature, setSelectedFeature] = useState<Node|null>(null);
  const [newFeature, setNewFeature] = useState<Node|null>(null);
  const [showNewFeatureInput, setShowNewFeatureInput] = useState(false);
  const [status, setStatus] = useState<FeatureStatus | undefined>(undefined);
  const [taskLink, setTaskLink] = useState<string | null>(null);

  useEffect(() => {
    resetForm();

    if(props.ctx.feature) {
      setSelectedFeature(props.ctx.feature);
      setStatus(getStatus(props.ctx.feature));
      setTaskLink(graph.getFeatureTaskLink(props.ctx.feature));
    } else {
      setShowNewFeatureInput(true);
      setStatus(undefined);
      setTaskLink(null);
    }

  }, [props.ctx.feature])

  const resetForm = () => {
    setSelectedFeature(null);
    setNewFeature(null);
    setStatus(undefined);
    setTaskLink(null);
  }

  const handleSubmit = (evt: FormEvent) => {
    evt.preventDefault();
    evt.stopPropagation();
  }

  const handleFeatureDeleted = () => {
    props.onCtxChanged(updateContext(props.ctx, {feature: null}));
  }

  const handleFeatureSelection = (selection: Node | null) => {
    // Also clear new state in this case
    if(!selection) {
      resetForm();
    } else if (!selectedFeature || selection.getId() !== selectedFeature.getId()) {
      setSelectedFeature(selection);
    }
  }

  const handleNewFeatureCreated = (createdFeature: Node) => {
    setShowNewFeatureInput(false);
    setSelectedFeature(createdFeature);
    setNewFeature(createdFeature);
  }

  const handleNameChanged = (newNodeName: string) => {
    if(props.ctx.feature) {
      graph.changeNodeName(props.ctx.feature, newNodeName);
      props.onCtxChanged(updateContext(props.ctx, {feature: props.ctx.feature.changeName(newNodeName)}))
    }
  }

  const handleStatusChanged = (newStatus: FeatureStatus | undefined) => {
    setStatus(newStatus);

    if(props.mode === "sidebar" && selectedFeature && newStatus) {
      graph.changeFeatureStatus(selectedFeature, newStatus);
    }
  }

  const handleTaskLinkChanged = (newLink: string | null) => {
    setTaskLink(newLink);

    if(selectedFeature) {
      graph.setFeatureTaskLink(selectedFeature, newLink);
    }
  }

  props.onFinish((): ImmutableDDDWizardContext | ImmutablePolicyContext | ImmutableFeatureContext => {
    if(!selectedFeature) {
      resetForm();
      return props.ctx;
    }

    let changedOrNewFeature = props.ctx.feature
      ? makeNodeFromJs({...convertNodeToJs(props.ctx.feature), ...{name: selectedFeature.getName()}})
      : makeNodeFromJs({
        id: graph.createCellId(),
        type: selectedFeature.getType(),
        name: selectedFeature.getName(),
      })

    if(!props.ctx.feature) {
      let currentContainerParent: Node | null = null;
      const allNodes = getAllNodesFromCtx(props.ctx);
      allNodes.forEach(node => {
        const parent = node.getParent();
        if(parent && parent.getType() === NodeType.boundedContext) {
          currentContainerParent = parent;
        }
      })

      if(currentContainerParent) {
        // Ensure all nodes have the same parent
        allNodes.forEach(node => {
          const parent = node.getParent();

          if(parent && parent.getId() !== currentContainerParent!.getId()) {
            graph.switchParent(node, parent);
          }
        })

        changedOrNewFeature = changedOrNewFeature.changeParent(currentContainerParent);
      }

      changedOrNewFeature = repositionFeature(changedOrNewFeature, props.ctx, graph);

      graph.addNode(changedOrNewFeature);

      getAllNodesFromCtx(props.ctx).forEach(child => graph.switchParent(child, changedOrNewFeature));
    }

    const currentStatus = getStatus(changedOrNewFeature);

    if(currentStatus) {
      graph.changeFeatureStatus(changedOrNewFeature, currentStatus);
    }

    if(status) {
      graph.changeFeatureStatus(changedOrNewFeature, status);
    }

    resetForm();
    return {...props.ctx, feature: changedOrNewFeature};
  })

  const nodeOptions = props.ctx.feature ? List.of(props.ctx.feature) : newFeature ? List.of(newFeature) : List([]);

  return <form className='ui form' onSubmit={handleSubmit}>
    {props.mode === 'modal' && <div className={'field'}>
      {!showNewFeatureInput && props.ctx.feature &&
          <ChangeNodeNameOrDeleteNode node={props.ctx.feature} board={props.ctx.board} uniqueName={true}
                                      onNameChanged={handleNameChanged} onNodeDeleted={handleFeatureDeleted}/>}
      {!showNewFeatureInput && !props.ctx.feature &&
          <NodeSelect nodeOptions={nodeOptions} selected={selectedFeature} onChange={handleFeatureSelection}
                      placeholder={props.t('insp.cody_wizard.node_select.select_feature') as string}/>}
      {showNewFeatureInput &&
          <NewNodeInput board={props.ctx.board} uniqueName={true} nameSuggestion={suggestNewFeatureName(props.ctx)}
                        nodeType={NodeType.feature} onEntered={handleNewFeatureCreated}
                        onCancel={() => setShowNewFeatureInput(false)}/>}
      {<Button basic={true} icon="add" labelPosition="left" size="mini"
               content={props.t('insp.cody_wizard.step_feature.new_feature') as string} color="blue" className="text"
               onClick={() => setShowNewFeatureInput(true)}
               disabled={!!selectedFeature || !!newFeature || showNewFeatureInput}/>}
    </div>}
    <EditorDivider content="Status" />
    <StatusSelect status={status} disabled={!selectedFeature} onStatusChanged={handleStatusChanged} />
    <EditorDivider content="Task" />
    <TaskLinkInput link={taskLink} onLinkChanged={handleTaskLinkChanged} disabled={!selectedFeature} />
    <div style={{marginTop: '100px'}} />
  </form>
};

export default withNamespaces()(Feature);

export const suggestNewFeatureName = (ctx: ImmutableDDDWizardContext | ImmutablePolicyContext | ImmutableFeatureContext): string | undefined => {
  if(isDDDAction(ctx)) {
    if(ctx.command) {
      return ctx.command.getName();
    }

    return suggestNewCommandName(ctx);
  }

  if(isPolicyContext(ctx)) {
    if(ctx.event) {
      return `On ${ctx.event.getName()}`;
    }

    if(ctx.policy) {
      return ctx.policy.getName();
    }
  }
}

export const repositionFeature = (feature: Node, ctx: WizardContext, graph: Graph): Node => {
  const allNodes = getAllNodesFromCtx(ctx);

  if(allNodes.count() === 0) {
    return feature;
  }

  // tslint:disable-next-line:one-variable-per-declaration
  let mostLeft: number, mostTop: number, mostRight: number, mostBottom: number;

  allNodes.forEach(node => {
    const graphNode = graph.getNode(node.getId());

    if(graphNode) {
      node = graphNode;
    }

    let geo = node.getGeometry();

    if(node.getParent() && node.getParent()!.getGeometry()) {
      geo = {x: geo.x + node.getParent()!.getGeometry().x, y: geo.y + node.getParent()!.getGeometry().y};
    }

    const size = node.getSize();

    if(geo.x < mostLeft || typeof mostLeft === 'undefined') {
      mostLeft = geo.x;
    }

    if(geo.y < mostTop || typeof mostTop === 'undefined') {
      mostTop = geo.y;
    }

    if(geo.x + size.width > mostRight || typeof mostRight === 'undefined') {
      mostRight = geo.x + size.width;
    }

    if(geo.y + size.height > mostBottom || typeof mostBottom === 'undefined') {
      mostBottom = geo.y + size.height;
    }
  })

  const padding = 160;

  const featureGeo: GraphPoint = {x: mostLeft! - padding, y: mostTop! - padding};
  const featureSize: NodeSize = {width: Math.abs((mostRight! + padding) - (mostLeft! - padding)), height: Math.abs((mostTop! - padding) - (mostBottom! + padding))};

  return feature.changeGeometry(featureGeo).changeSize(featureSize);
}

export const getAllNodesFromCtx = (ctx: WizardContext): List<Node> => {
  switch (ctx.type) {
    case "dddAction":
      return getAllNodesFromDDDActionCtx(ctx as ImmutableDDDWizardContext);
    case "policy":
      return getAllNodesFromPolicyCtx(ctx as ImmutablePolicyContext);
    case "feature":
      return (ctx as ImmutableFeatureContext).feature.children();
    default:
      return List([]);
  }
}

const getAllNodesFromPolicyCtx = (ctx: ImmutablePolicyContext): List<Node> => {
  const allNodes: Node[] = [];

  if(ctx.event) {
    allNodes.push(ctx.event);
  }

  if(ctx.policy) {
    allNodes.push(ctx.policy);
  }

  if(ctx.policyDependencies) {
    allNodes.push(...Object.values(ctx.policyDependencies).map(dep => dep.node))
  }

  allNodes.push(...ctx.policyTargets.toArray());

  return List(allNodes);
}

const getAllNodesFromDDDActionCtx = (ctx: ImmutableDDDWizardContext): List<Node> => {
  const allNodes: Node[] = [];

  if(ctx.uiViews) {
    allNodes.push(...ctx.uiViews.toArray());
  }

  if(ctx.parentUi) {
    allNodes.push(ctx.parentUi);
  }

  if(ctx.ui) {
    allNodes.push(ctx.ui);
  }

  if(ctx.command) {
    allNodes.push(ctx.command);
  }

  if(ctx.aggregate) {
    allNodes.push(ctx.aggregate);
  }

  if(ctx.commandDependencies) {
    allNodes.push(...Object.values(ctx.commandDependencies).map(dep => dep.node))
  }

  allNodes.push(...ctx.events.toArray());

  if(ctx.state) {
    allNodes.push(ctx.state);
  }

  if(ctx.stateList) {
    allNodes.push(ctx.stateList)
  }

  return List(allNodes);
}

const getStatus = (feature: Node): FeatureStatus | undefined => {
  const tags = feature.getTags().toArray();

  for (const tag of tags) {
    if(POSSIBLE_STATUS.includes(tag)) {
      return tag as FeatureStatus;
    }
  }
}


