import {List} from "immutable";
import * as React from 'react';
import {useEffect, useRef, useState} from 'react';
import {withNamespaces, WithNamespaces} from "react-i18next";
import {FormField, Message} from "semantic-ui-react";
import CodeEditor from "../../../../core/components/CodeEditor/CodeEditor";
import {Node, NodeType} from "../../../model/Graph";
import {ImmutablePolicyContext} from "../../../service/cody-wizard/context/policy-context";
import {fqcn} from "../../../service/cody-wizard/node/fqcn";
import {names} from "../../../service/cody-wizard/node/names";
import {Rule} from "../../../service/cody-wizard/rule-engine/configuration";
import {RulesDependency} from "../../../service/cody-wizard/rule-engine/types";
import {makePolicyRulesSchema} from "./Validation/policy-rules-schema";


interface OwnProps {
  rules: Rule[];
  policyTargets: List<Node>;
  dependencies: Record<string, RulesDependency>;
  onRulesChanged: (rules: Rule[]) => void;
  autofocus?: boolean;
  ctx: ImmutablePolicyContext;
}

type PolicyRulesEditorProps = OwnProps & WithNamespaces;

const PolicyRulesEditor = (props: PolicyRulesEditorProps) => {
  const codeEditorRef = useRef<CodeEditor|null>(null);
  const [editorHasFocus, setEditorHasFocus] = useState(false);
  const [invalidJson, setInvalidJson] = useState(false);

  const possibleServices: string[] = [];

  for (const dependenciesKey in props.dependencies) {
    if(props.dependencies[dependenciesKey].node.getType() === NodeType.externalSystem) {
      possibleServices.push(props.dependencies[dependenciesKey].alias || dependenciesKey);
    }
  }

  useEffect(() => {
    if (!codeEditorRef.current) {
      return;
    }

    codeEditorRef.current!.initializeModel({
      fileId: `policy-rules.json`,
      language: "json",
      value: JSON.stringify(props.rules, null, 2),
      schema: makePolicyRulesSchema(
        props.policyTargets.filter(t => t.getType() === NodeType.command).map(c => fqcn(c, props.ctx.board.codyEngineServiceName())).toArray(),
        possibleServices
      ),
    });

    setInvalidJson(false);
  }, [props.rules, props.policyTargets, possibleServices]);

  useEffect(() => {
    if(props.autofocus && codeEditorRef.current) {
      codeEditorRef.current.focus();
    }
  }, [props.autofocus])

  const propagateChanges = (editorStr: string, silentError?: boolean) => {
    try {
      const changedRules = JSON.parse(editorStr);
      props.onRulesChanged(changedRules);
    } catch (e) {
      console.error("[CodyWizard] Business Rules Editor invalid JSON: ", e);
      if(!silentError) {
        setInvalidJson(true);
      }
    }
  }

  return <FormField>
    {invalidJson && <Message error={true} size="small" content={props.t('insp.cody_wizard.schema_editor.invalid_json') as string} icon="warning" style={{display: 'flex'}}/>}
    <CodeEditor
      containerId={'cody-policy-rules-editor'}
      ref={codeEditorRef}
      options={{
        fontSize: 12,
        folding: true,
        glyphMargin: false,
        lineNumbers: true,
        lineDecorationsWidth: 3,
        minimap: {
          enabled: false
        },
        formatOnPaste: true,
        scrollBeyondLastLine: false,
        automaticLayout: true,
        scrollbar: {
          alwaysConsumeMouseWheel: false
        }
      }}
      className={"code editor" + (editorHasFocus? ' focus' : '')}
      onBlur={() => {
        if(codeEditorRef.current) {
          const changedRulesStr = codeEditorRef.current.retrievePayload();

          console.log("[CodyWizard] editor rules", changedRulesStr);

          setEditorHasFocus(false);

          propagateChanges(changedRulesStr);
        }
      }}
      onFocus={() => {
        setEditorHasFocus(true);
      }}
    />
  </FormField>
};

export default withNamespaces()(PolicyRulesEditor);
