import * as React from 'react';
import {useEffect, useRef, useState} from "react";
import {withNamespaces, WithNamespaces} from "react-i18next";
import {FormField, Message} from "semantic-ui-react";
import CodeEditor from "../../../../core/components/CodeEditor/CodeEditor";
import {useElementsTree} from "../../../hooks/useElementsTree";
import {BoardId} from "../../../model/Board";
import {RulesDependency} from "../../../service/cody-wizard/rule-engine/types";
import {Schema} from "../../../service/cody-wizard/schema/schema";
import {makeDependenciesSchema} from "./Validation/dependencies-schema";

interface OwnProps {
  boardId: BoardId;
  dependencies: Record<string, RulesDependency>;
  messageSchema: Schema;
  onDependenciesChanged: (dependencies: Record<string, RulesDependency>) => void;
  autofocus?: boolean;
}

type DependenciesEditorProps = OwnProps & WithNamespaces;

const DependenciesEditor = (props: DependenciesEditorProps) => {
  const elementsTree = useElementsTree(props.boardId);
  const codeEditorRef = useRef<CodeEditor|null>(null);
  const [editorHasFocus, setEditorHasFocus] = useState(false);
  const [invalidJson, setInvalidJson] = useState(false);

  useEffect(() => {
    if (!codeEditorRef.current) {
      return;
    }

    codeEditorRef.current!.initializeModel({
      fileId: `query-schema.json`,
      language: "json",
      value: props.dependencies ? dependenciesToString(props.dependencies) : '',
      schema: props.dependencies ? makeDependenciesSchema(props.dependencies, props.messageSchema, elementsTree) : undefined,
    });

    setInvalidJson(false);
  }, [props.dependencies]);

  useEffect(() => {
    if(props.autofocus && codeEditorRef.current) {
      codeEditorRef.current.focus();
    }
  }, [props.autofocus])

  const propagateChanges = (editorStr: string, silentError?: boolean) => {
    try {
      const changedDependenciesConfig = JSON.parse(editorStr);
      props.onDependenciesChanged(mergeConfig(props.dependencies, changedDependenciesConfig));
    } catch (e) {
      console.error("[CodyWizard] Dependencies Editor invalid JSON: ", e);
      if(!silentError) {
        setInvalidJson(true);
      }
    }
  }

  return <FormField>
    {invalidJson && <Message error={true} size="small" content={props.t('insp.cody_wizard.query_schema_editor.invalid_json') as string} icon="warning" style={{display: 'flex'}}/>}
    <CodeEditor
      containerId={'cody-dependencies-editor'}
      ref={codeEditorRef}
      options={{
        fontSize: 12,
        folding: true,
        glyphMargin: false,
        lineNumbers: true,
        lineDecorationsWidth: 3,
        minimap: {
          enabled: false
        },
        formatOnPaste: true,
        scrollBeyondLastLine: false,
        automaticLayout: true,
        scrollbar: {
          alwaysConsumeMouseWheel: false
        }
      }}
      className={"code editor" + (editorHasFocus? ' focus' : '')}
      onBlur={() => {
        if(codeEditorRef.current) {
          const changedDependenciesStr = codeEditorRef.current.retrievePayload();

          setEditorHasFocus(false);

          propagateChanges(changedDependenciesStr);
        }
      }}
      onFocus={() => {
        setEditorHasFocus(true);
      }}
    />
  </FormField>
};

export default withNamespaces()(DependenciesEditor);

type DependencyConfig = Record<string, {alias?: string, options?: Record<string, any>}>;

const dependenciesToString = (dependencies: Record<string, RulesDependency>): string => {
  const depsConfig: DependencyConfig  = {};

  // tslint:disable-next-line:forin
  for (const dependenciesKey in dependencies) {
    depsConfig[dependenciesKey] = {
      alias: dependencies[dependenciesKey].alias,
      options: dependencies[dependenciesKey].options,
    }
  }

  return JSON.stringify(depsConfig, null, 2);
}

const mergeConfig = (dependencies: Record<string, RulesDependency>, changedConfig: DependencyConfig): Record<string, RulesDependency> => {
  const mergedConfig: Record<string, RulesDependency> = {};

  // tslint:disable-next-line:forin
  for (const dependenciesKey in dependencies) {
    mergedConfig[dependenciesKey] = {
      node: dependencies[dependenciesKey].node,
      alias: changedConfig[dependenciesKey] ? changedConfig[dependenciesKey].alias : undefined,
      options: changedConfig[dependenciesKey] ? changedConfig[dependenciesKey].options : undefined,
    }
  }

  return mergedConfig;
}
