import {JSONSchema7} from "json-schema";
import {makeAssignVariable, makeRuleThenPropMapping} from "./rule-engine-definitions";
import {iconNameEnum} from "./ui-schema";

export const makeUiConfigSchema = (possibleStateRefs: string[]): string => {
  const routeSchema: JSONSchema7 = {type: "string", minLength: 1, pattern: "^/[a-z]+"};

  const uiConfig: JSONSchema7 = {
    oneOf: [
      // Toplevel page
      {
        type: "object",
        additionalProperties: false,
        required: ["route", "sidebar"],
        properties: {
          route: routeSchema,
          breadcrumb: {type: "string", minLength: 1},
          sidebar: {
            type: "object",
            additionalProperties: false,
            properties: {
              label: {type: "string", minLength: 1},
              icon: iconNameEnum()
            }
          }
        }
      },
      // Sublevel page
      {
        type: "object",
        additionalProperties: false,
        required: ["route", "routeParams"],
        properties: {
          route: routeSchema,
          routeParams: {type: "array", items: {type: "string"}},
          breadcrumb: {
            oneOf: [
              {type: "string", minLength: 1},
              {
                type: "object",
                additionalProperties: false,
                required: ["data", "label"],
                properties: {
                  data: {type: "string", enum: possibleStateRefs},
                  label: {
                    oneOf: [
                      {type: "string", minLength: 1},
                      {$ref: "#/definitions/rule_then_assign_variable"}
                    ]
                  }
                }
              }
            ]
          }
        }
      }
    ],
    definitions: {
      ...makeAssignVariable('value'),
      ...makeRuleThenPropMapping()
    }
  }

  return JSON.stringify(uiConfig);
}
