import { userInfo } from "os";
import React, {useEffect, useRef, useState} from "react";
import {Image} from "semantic-ui-react";
import WebSocketConnection from "../../../api/ConfiguredJitsiSocket";
import {JitsiUser} from "../../model/JitsiUser";
import './JitsiVideo.css';
import * as _ from "lodash";

export interface Position {
    left: number;
    top: number;
}

export interface MousePoint {
    x: number;
    y: number;
}

export type NewPosition = Position;

export interface PositionChange {
    startPositionAvatar: Position,
    startPositionMouse: MousePoint,
    currentPositionMouse: MousePoint,
}

interface JitsiVideoProps {
    user: JitsiUser;
    sessionId: string; 
    isLocalUser: boolean;
    onPositionChange?: (change: PositionChange) => NewPosition;
    volume: number;
    /* isSpeaking: boolean; */
    isAnyMegaphoneActive: boolean;
    isSpeaking: boolean;
}

const defaultPositionChange: PositionChange = {
    startPositionAvatar: {left: 0, top: 0},
    startPositionMouse: {x: 0, y: 0},
    currentPositionMouse: {x: 0, y: 0}
}

const emojiIconSettings: any =  { 
    "thumbs up": "green thumbs up thumbUp-indicator",
    "thumbs down": "red thumbs down thumbDown-indicator",
    "beer": "yellow beer beer-indicator",
}

const throttlePosition = _.throttle( (id, sid, np) => WebSocketConnection.sendPositionUpdate(id, sid, np.left, np.top), 66, { 'trailing': true })

const JitsiMeetJS = (window as any).JitsiMeetJS;

const JitsiVideo = (props: JitsiVideoProps) => {
    const videoRef = useRef<any>(null);
    const audioRef = useRef<any>(null);
    const [isDragging, setIsDragging] = useState<boolean>();
    const positionChange = useRef<PositionChange>(defaultPositionChange);
    const [isFocused, setIsFocused] = useState<boolean>(false);
    const changePosition = (x: number, y: number) => {
        positionChange.current.currentPositionMouse.x = x;
        positionChange.current.currentPositionMouse.y = y;

        if(props.onPositionChange) {
            const newPosition = props.onPositionChange(positionChange.current);
            throttlePosition(props.user.audioTrack!.getParticipantId(),  props.sessionId, newPosition);
        }
    }
    
    useEffect(() => { // if != null
        if(props.user.videoTrack == null || props.user.audioTrack == null){return};
        props.user.videoTrack!.attach(videoRef.current);
        if(!props.isLocalUser){
            props.user.audioTrack!.attach(audioRef.current);
        }
    }, [props.user.audioTrack, props.user.videoTrack]);

    useEffect(() => {
        console.log("jitsivolume : volume changed => changes ", props.volume)
        if (!videoRef.current) {
            return;
        }

        if(props.isLocalUser) {
            return;
        }

        let volume = props.volume * (props.isAnyMegaphoneActive ? 0.7 : 1.0);
        if (props.user.megaphoneOn) {
            console.log("Update_remote_user Megaphone on and Volume should be 1");
            volume = 1.0;
        }
        
        console.log("Update_remote_user Volume ", volume);
        (audioRef.current as any).volume = volume;
        
        if(volume === 0 && !(videoRef.current as any).paused) {
            console.log("jitsipauseplay : pause ",(videoRef.current as any).paused, volume, videoRef.current);
            (videoRef.current as any).pause();
            (audioRef.current as any).pause();
        }

        if(volume > 0 && (videoRef.current as any).paused) {
            console.log("jitsipauseplay : play",(videoRef.current as any).paused, volume, videoRef.current);
            (videoRef.current as any).play();
            (audioRef.current as any).play();
        }
    }, [props.volume, props.user.megaphoneOn, props.isAnyMegaphoneActive]);

    useEffect(() => {
        const mouseUpListener = () => {
            setIsDragging(false);
        }

        const mouseMoveListener = (e: MouseEvent) => {
            if (props.isLocalUser && isDragging) {
                changePosition(e.clientX, e.clientY);
            }
        }

        const touchMoveListener = (e: TouchEvent) => {
            if (props.isLocalUser && isDragging) {
                changePosition(e.touches[0].clientX, e.touches[0].clientY);
            }
        }

        document.addEventListener('mouseup', mouseUpListener);
        document.addEventListener('touchend', mouseUpListener);
        window.addEventListener('mouseup', mouseUpListener);
        document.addEventListener('mousemove', mouseMoveListener);
        document.addEventListener('touchmove', touchMoveListener);

        return () => {
            document.removeEventListener('mouseup', mouseUpListener);
            document.removeEventListener('touchend', mouseUpListener);
            window.removeEventListener('mouseup', mouseUpListener);
            document.removeEventListener('mousemove', mouseMoveListener);
            document.removeEventListener('touchmove', touchMoveListener);
        }
    }) 

    const applyFocusedSize = (size: number): number => {
        return isFocused? size * 2 : size;
    }
    const fullSize = 150;
    const currentSize = applyFocusedSize(props.isLocalUser? fullSize : 50 + (props.user.megaphoneOn? 100 : (props.volume * 100)));
    const borderRadius = Math.round(currentSize / 2);
    const showVideo = props.user.cameraOn && (props.isLocalUser || props.volume > 0 || props.user.megaphoneOn);
    const darkenAvatar = !props.isLocalUser && props.volume === 0 && !props.user.megaphoneOn;
    const toggleIsFocused = () => {
        setIsFocused(!isFocused);
        console.log("focus toggled");
    }

    return (
        <div
            style={{
                width: currentSize,
                height: currentSize,
                borderRadius,
                position: 'absolute',
                left: props.user.position.left - currentSize / 2,
                top: props.user.position.top - currentSize / 2,
                pointerEvents: 'auto',
                cursor: props.isLocalUser? "move" : "default",
                zIndex: props.isLocalUser? 10 : 3,
                WebkitTransform: 'translateZ(0)',
            }}

            onMouseDown={(e) => {
                    positionChange.current.startPositionAvatar = props.user.position;
                    positionChange.current.startPositionMouse = {
                        x: e.clientX,
                        y: e.clientY,
                    }
                    setIsDragging(true);
                }
            }

            onTouchStart={(e) => {
                positionChange.current.startPositionAvatar = props.user.position;
                positionChange.current.startPositionMouse = {
                    x: e.touches[0].clientX,
                    y: e.touches[0].clientY,
                }
                setIsDragging(true);
            }}

            onDoubleClick={toggleIsFocused}
        >
            <div style={{
                width: currentSize,
                height: currentSize,
                borderRadius,
                overflow: 'hidden',
                backgroundColor: '#9cc8f5',
                pointerEvents: 'auto',
                ...(currentSize>50? (props.user.micOn ? (props.isSpeaking ? {border: '5px solid #9cc8f5'} : {border: '5px solid #CCCCCC'}) : {border: '5px solid #666666'}) : { border: '5px solid #AAAAAA'} ),
                WebkitTransform: 'translateZ(0)',
                zIndex: props.isLocalUser? 10 : 3,
            }}>
                {/*
                 // @ts-ignore */}
                <video
                    autoPlay={true}
                    disablePictureInPicture={true /* Not working in FF .... */}
                    ref={videoRef}
                    muted={props.isLocalUser}
                    style={{ width: currentSize, height: currentSize, objectFit: 'cover', objectPosition: 'center', pointerEvents: 'auto', display: showVideo? 'block' : 'none', zIndex: props.isLocalUser? 9 : 2 }}
                />
                <audio
                    autoPlay={true}
                    muted={props.isLocalUser}
                    ref={audioRef}
                />
                {!showVideo && <Image //!showVideo &&
                    src={props.user.avatar}//'https://einfachtierisch.de/media/cache/article_main_image_tablet/cms/2015/09/Graues-Kaetzchen-auf-Decke-shutterstock-dezi-134747402.jpg?739390'}//
                    avatar={true}
                    verticalAlign="middle"
                    style={{
                        width: currentSize,
                        height: currentSize,
                        objectFit: 'cover',
                        objectPosition: 'center',
                        pointerEvents: 'none',
                        filter: darkenAvatar? 'brightness(0.8)' : 'none'
                    }}
                />}
            </div>
            <div className={'disable-selection'} style={{ position: "absolute", bottom: '-26px', left: 0, width: '100%', textAlign: 'center', fontSize: '16px', pointerEvents: 'auto'}}>{props.user.name}</div>
            
            <i className={'icon bullhorn megaphone-indicator' + (props.user.megaphoneOn ? ' active' : '')} />
            
            <i className={'green icon hand paper handUp-indicator' + (props.user.handUpOn ? ' active' : '')} />
            
            <i className={'icon ' + (emojiIconSettings[props.user.showIconName]) + ' ' + (props.user.emojiOn ? ' active' : '')} />
        
        </div>
    );
};

export default JitsiVideo;
