import {Node, NodeType} from "../../../model/Graph";
import {WizardContext} from "../context/wizard-context";
import {names} from "../node/names";
import {RulesDependency} from "./types";

export const suggestRulesDependencyConfig = (node: Node, ctx: WizardContext): RulesDependency => {
  return {
    node,
    alias: (node.getType() === NodeType.document) ? names(node.getName()).propertyName : undefined,
  }
}
