import {JSONSchema7} from "json-schema";
import * as React from 'react';
import {useEffect, useRef, useState} from "react";
import {withNamespaces, WithNamespaces} from "react-i18next";
import {useSelector} from "react-redux";
import {FormField, Message} from "semantic-ui-react";
import CodeEditor from "../../../../core/components/CodeEditor/CodeEditor";
import {useElementsTree} from "../../../hooks/useElementsTree";
import {BoardId, BoardName, ServiceName} from "../../../model/Board";
import {ElementsTree} from "../../../model/ElementsTree";
import {NodeType} from "../../../model/Graph";
import {makeBoardByIdSelector} from "../../../selectors/board";
import {fqcn} from "../../../service/cody-wizard/node/fqcn";
import {names} from "../../../service/cody-wizard/node/names";
import {Schema} from "../../../service/cody-wizard/schema/schema";
import {UiSchema} from "../../../service/cody-wizard/ui/ui-schema";
import {getSchemaRefName} from "../../../service/cody-wizard/vo/get-schema-ref-name";
import {getVoMetadata} from "../../../service/cody-wizard/vo/get-vo-metadata";
import {isListVo} from "../../../service/cody-wizard/vo/is-list-vo";
import {makeCommandButtonUiSchema, makeTableUiSchema, makeUiSchema} from "./Validation/ui-schema";


interface OwnProps {
  boardId: BoardId;
  nodeSchema: Schema;
  schema: UiSchema;
  onSchemaChanged: (schema: UiSchema) => void;
  autofocus?: boolean;
  buttonSchema?: boolean;
  tableSchema?: boolean;
}

type UiSchemaEditorProps = OwnProps & WithNamespaces;

const UiSchemaEditor = (props: UiSchemaEditorProps) => {
  const elementsTree = useElementsTree(props.boardId);
  const board = useSelector(makeBoardByIdSelector(props.boardId));
  const codeEditorRef = useRef<CodeEditor|null>(null);
  const [editorHasFocus, setEditorHasFocus] = useState(false);
  const [invalidJson, setInvalidJson] = useState(false);

  useEffect(() => {
    if (!codeEditorRef.current) {
      return;
    }

    codeEditorRef.current!.initializeModel({
      fileId: `ui-schema.json`,
      language: "json",
      value: JSON.stringify(props.schema, null, 2) || '{\n  \n}\n',
      schema: makeUiSchema({
        ...getPropertiesUiSchema(props.nodeSchema),
        ...(props.buttonSchema? makeCommandButtonUiSchema() : {}),
        ...(props.tableSchema? makeTableUiSchema(getItemProperties(props.nodeSchema, elementsTree), getPossiblePages(elementsTree), getPossibleListVOs(elementsTree)) : {}),
      }, getPossibleListVOs(elementsTree), getPossibleCommands(elementsTree, board.codyEngineServiceName())),
    });

    setInvalidJson(false);
  }, [props.schema, props.nodeSchema, board.name]);

  useEffect(() => {
    if(props.autofocus && codeEditorRef.current) {
      codeEditorRef.current.focus();
    }
  }, [props.autofocus])

  const propagateChanges = (editorStr: string, silentError?: boolean) => {
    try {
      const changedSchema = JSON.parse(editorStr);
      props.onSchemaChanged(changedSchema);
    } catch (e) {
      console.error("[CodyWizard] UI Schema Editor invalid JSON: ", e);
      if(!silentError) {
        setInvalidJson(true);
      }
    }
  }

  return <FormField>
    {invalidJson && <Message error={true} size="small" content={props.t('insp.cody_wizard.ui_schema_editor.invalid_json') as string} icon="warning" style={{display: 'flex'}}/>}
    <CodeEditor
      containerId={'cody-ui-schema-editor'}
      ref={codeEditorRef}
      options={{
        fontSize: 12,
        folding: true,
        glyphMargin: false,
        lineNumbers: true,
        lineDecorationsWidth: 3,
        minimap: {
          enabled: false
        },
        formatOnPaste: true,
        scrollBeyondLastLine: false,
        automaticLayout: true,
        scrollbar: {
          alwaysConsumeMouseWheel: false
        }
      }}
      className={"code editor" + (editorHasFocus? ' focus' : '')}
      onBlur={() => {
        if(codeEditorRef.current) {
          const changedSchemaStr = codeEditorRef.current.retrievePayload();

          setEditorHasFocus(false);

          propagateChanges(changedSchemaStr);
        }
      }}
      onFocus={() => {
        setEditorHasFocus(true);
      }}
    />
  </FormField>
};

export default withNamespaces()(UiSchemaEditor);

const getPropertiesUiSchema = (nodeSchema: Schema): Record<string, JSONSchema7> => {
  const props = nodeSchema.getObjectProperties();

  const propsSchema: Record<string, JSONSchema7> = {};

  props.forEach(prop => {
    propsSchema[prop] = {
      $ref: "#/definitions/ui_schema"
    }
  })

  return propsSchema;
}

const getPossibleListVOs = (elementsTree: ElementsTree): string[] => {
  const possibleRefs: string[] = [];

  elementsTree.getFilteredElementsByType(NodeType.document).forEach(ele => {
    const voMeta = getVoMetadata(ele);

    if(!voMeta.querySchema) {
      return;
    }

    if(!isListVo(ele)) {
      return;
    }

    const possibleRef = getSchemaRefName(ele, voMeta);

    if(!possibleRefs.includes(possibleRef)) {
      possibleRefs.push(possibleRef);
    }
  });

  return possibleRefs;
}

const getItemProperties = (nodeSchema: Schema, elementsTree: ElementsTree): string[] => {
  if(!nodeSchema.isList()) {
    return [];
  }

  let listItemSchema = nodeSchema.getListItemsSchema(new Schema({}));

  if(listItemSchema.isRef()) {
    listItemSchema =  listItemSchema.resolveRef(elementsTree);
  }

  return listItemSchema.getObjectProperties();
}

const getPossiblePages = (elementsTree: ElementsTree): string[] => {
  return elementsTree.getFilteredElementsByType(NodeType.ui).map(ui => names(ui.getName()).className).toArray();
}

const getPossibleCommands = (elementsTree: ElementsTree, boardName: ServiceName): string[] => {
  return elementsTree.getFilteredElementsByType(NodeType.command).map(cmd => fqcn(cmd, boardName)).toArray();
}
