import {List} from "immutable";
import * as React from 'react';
import {FormEvent, useEffect, useState} from 'react';
import {withNamespaces, WithNamespaces} from "react-i18next";
import {Button} from "semantic-ui-react";
import {useElementsTree} from "../../../hooks/useElementsTree";
import {useGraph} from "../../../hooks/useGraph";
import {
  convertNodeToJs,
  Graph,
  makeNodeFromJs,
  NextToPosition,
  Node,
  NodeMetadata,
  NodeType
} from "../../../model/Graph";
import {AggregateMetadata, getAggregateMetadata} from "../../../service/cody-wizard/aggregate/get-aggregate-metadata";
import {getCommandMetadata} from "../../../service/cody-wizard/command/get-command-metadata";
import {dddActionFromNode, ImmutableDDDWizardContext} from "../../../service/cody-wizard/context/ddd-action";
import {updateContext} from "../../../service/cody-wizard/context/wizard-context";
import {markAsConnected} from "../../../service/cody-wizard/node/mark-as-connected";
import {markAsDisconnected} from "../../../service/cody-wizard/node/mark-as-disconnected";
import {names} from "../../../service/cody-wizard/node/names";
import {Rule} from "../../../service/cody-wizard/rule-engine/configuration";
import {
  convertDependenciesToMetadataConfig
} from "../../../service/cody-wizard/rule-engine/convert-dependencies-to-metadata-config";
import {getDependencyName} from "../../../service/cody-wizard/rule-engine/get-dependency-name";
import {suggestRulesDependencyConfig} from "../../../service/cody-wizard/rule-engine/suggest-rules-dependency-config";
import {RulesDependency} from "../../../service/cody-wizard/rule-engine/types";
import {Schema} from "../../../service/cody-wizard/schema/schema";
import {ensureSingularNoun, isAdjective, isNoun, tagSentence} from "../../../service/cody-wizard/tagger";
import BusinessRulesEditor from "../Editor/BusinessRulesEditor";
import DependenciesEditor from "../Editor/DependenciesEditor";
import EditorDivider from "../Editor/EditorDivider";
import ChangeNodeNameOrDeleteNode from "../Node/ChangeNodeNameOrDeleteNode";
import MultipleNodeSelect from "../Node/MultipleNodeSelect";
import NewNodeInput from "../Node/NewNodeInput";
import NodeSelect from "../Node/NodeSelect";
import {DEFAULT_CONNECT_MARGIN, WithWizardStepProps} from "../WizardModal";
import {repositionFeature} from "./Feature";

interface OwnProps {

}

type AggregateProps = OwnProps & WithWizardStepProps<ImmutableDDDWizardContext> & WithNamespaces;

const Aggregate = (props: AggregateProps) => {
  const aggregate = props.ctx.aggregate;

  const [graph] = useGraph();
  const elementsTree = useElementsTree(props.ctx.board.uid);
  const [selectedAggregate, setSelectedAggregate] = useState<Node|null>(null);
  const [newAggregate, setNewAggregate] = useState<Node|null>(null);
  const [showNewAggregateInput, setShowNewAggregateInput] = useState(false);
  const [metadata, setMetadata] = useState<AggregateMetadata>({});
  const [autoFocusBusinessRules, setAutoFocusBusinessRules] = useState(false);
  const [dependencies, setDependencies] = useState<Record<string, RulesDependency>>({});
  const [showDependencies, setShowDependencies] = useState(false);

  const updateMetadata = (meta: AggregateMetadata) => {
    if(props.onUpdateMetadata) {
      props.onUpdateMetadata(JSON.stringify(meta, null, 2), true);
    }

    setMetadata(meta);
  }

  const updateDependencies = (deps: Record<string, RulesDependency>) => {
    if(props.mode === "sidebar" && selectedAggregate) {
      const shouldRepositionFeature = updateDependenciesOnGraph(selectedAggregate, props.ctx, List(Object.values(deps).map(dep => dep.node)), graph);
      // Refresh context
      const changedCtx = dddActionFromNode(graph.getNode(selectedAggregate.getId())!, props.ctx.board);

      if(props.ctx.command) {
        const cmdMeta = getCommandMetadata(props.ctx.command);
        const currentCmdDeps = JSON.stringify(cmdMeta.dependencies || {});
        const depsMetaConfig = convertDependenciesToMetadataConfig(deps);

        if(currentCmdDeps !== JSON.stringify(depsMetaConfig)) {
          cmdMeta.dependencies = depsMetaConfig;
          graph.changeNodeMetadata(props.ctx.command, JSON.stringify(cmdMeta, null, 2));
        }
      }

      if(shouldRepositionFeature && props.ctx.feature) {
        const repositionedFeature = repositionFeature(
          props.ctx.feature,
          dddActionFromNode(graph.getNode(selectedAggregate.getId())!, props.ctx.board),
          graph
        );

        graph.resizeNode(repositionedFeature, repositionedFeature.getSize());
      }

      props.onCtxChanged(changedCtx);

    }

    setDependencies(deps);
  }

  useEffect(() => {
    resetForm();

    if(aggregate) {
      setSelectedAggregate(aggregate);
      const meta = getAggregateMetadata(aggregate);
      setMetadata(meta);
    } else if (props.ctx.state) {
      const aggregateFromState = makeNodeFromJs({
        id: graph.createCellId(),
        name: props.ctx.state.getName(),
        type: NodeType.aggregate
      });
      handleNewAggregateCreated(aggregateFromState);
    }

    if(Object.keys(props.ctx.commandDependencies).length) {
      setShowDependencies(true);
    }
  }, [aggregate]);

  useEffect(() => {
    setDependencies(props.ctx.commandDependencies || {});
  }, [props.ctx.commandDependencies]);

  const resetForm = () => {
    setSelectedAggregate(null);
    setNewAggregate(null);
    setShowNewAggregateInput(false);
    setMetadata({});
    setAutoFocusBusinessRules(false);
    setDependencies({});
    setShowDependencies(false);
  }

  const handleSubmit = (evt: FormEvent) => {
    evt.preventDefault();
    evt.stopPropagation();
  }

  const handleAggregateSelection = (selection: Node | null) => {
    // Also clear new aggregate in this case
    if(!selection) {
      resetForm();
    } else if (!selectedAggregate || selection.getId() !== selectedAggregate.getId()) {
      setSelectedAggregate(selection);
      const selectedMeta = getAggregateMetadata(selection);

      // Always reset rules
      selectedMeta.rules = suggestEventRecordingRules(props.ctx);

      setMetadata(selectedMeta);
    }
  }

  const handleNewAggregateCreated = (createdAggregate: Node) => {
    console.log("[CodyWizard] new aggregate created: ", convertNodeToJs(createdAggregate));
    setShowNewAggregateInput(false);
    setSelectedAggregate(createdAggregate);
    setNewAggregate(createdAggregate);
    setMetadata({
      rules: suggestEventRecordingRules(props.ctx)
    });
    setAutoFocusBusinessRules(true);
  }

  const handleNameChanged = (newNodeName: string) => {
    if(aggregate) {
      graph.changeNodeName(aggregate, newNodeName);
      props.onCtxChanged(updateContext(props.ctx, {aggregate: aggregate.changeName(newNodeName)}));
    }
  }

  const handleAggregateDeleted = () => {
    if(props.ctx.commandDependencies) {
      Object.values(props.ctx.commandDependencies).forEach(dep => {
        graph.deleteNode(dep.node);
      })
    }

    props.onCtxChanged(updateContext(props.ctx, {aggregate: null, commandDependencies: {}}));
  }

  const handleRulesChanged = (newRuleSet: Rule[]) => {
    updateMetadata({...metadata, rules: newRuleSet});
  }

  const handleDependenciesChanged = (changedDependencies: List<Node>) => {
    // @todo: Diff changedDependencies <-> dependencies, suggest alias etc. for new, delete removed
    const newDependencies: Record<string, RulesDependency> = {};

    changedDependencies.forEach(dep => {
      const depName = getDependencyName(dep, props.ctx);
      if(!dependencies[depName]) {
        newDependencies[depName] = suggestRulesDependencyConfig(dep, props.ctx);
      } else {
        newDependencies[depName] = dependencies[depName];
      }
    })

    updateDependencies(newDependencies);
  }

  const handleDependenciesConfigChanged = (changedDependencies: Record<string, RulesDependency>) => {
    updateDependencies(changedDependencies);
  }

  props.onFinish((): ImmutableDDDWizardContext => {
    if(!selectedAggregate) {
      resetForm();
      return props.ctx;
    }

    let changedOrNewAggregate = aggregate
      ? makeNodeFromJs({
        ...convertNodeToJs(aggregate), ...{
          name: selectedAggregate.getName(),
          metadata: JSON.stringify(metadata, null, 2)
        }
      })
      : makeNodeFromJs({
      id: graph.createCellId(),
      type: selectedAggregate.getType(),
      name: selectedAggregate.getName(),
      metadata: JSON.stringify(metadata, null, 2),
    });

    if(!aggregate) {
      let isAdded = false;
      if(props.ctx.events.first(undefined)) {
        graph.addNodeNextToAnother(changedOrNewAggregate, props.ctx.events.first(), NextToPosition.above, DEFAULT_CONNECT_MARGIN);
        graph.connectNodes(changedOrNewAggregate, props.ctx.events.first());
        isAdded = true;
      }

      if(props.ctx.command) {
        if(!isAdded) {
          graph.addNodeNextToAnother(changedOrNewAggregate, props.ctx.command, NextToPosition.below, DEFAULT_CONNECT_MARGIN);
          isAdded = true;
        }

        graph.connectNodes(props.ctx.command, changedOrNewAggregate);
      }

      if(!isAdded) {
        console.error("[CodyWizard] Unable to create Aggregate. A command or event card is missing to connect the aggregate card to.");
        return props.ctx;
      }

      // Explicitly mark aggregate as disconnected, since each AR card defines its own business rules
      changedOrNewAggregate = markAsDisconnected(changedOrNewAggregate, graph);
    }

    let shouldRepositionFeature = updateDependenciesOnGraph(changedOrNewAggregate, props.ctx, List(Object.values(dependencies).map(dep => dep.node)), graph);

    graph.changeNodeMetadata(changedOrNewAggregate, changedOrNewAggregate.getMetadata() as NodeMetadata);

    let command = props.ctx.command;

    if(props.ctx.command) {
      const cmdMeta = getCommandMetadata(props.ctx.command);
      const currentCmdDeps = JSON.stringify(cmdMeta.dependencies || {});
      const depsMetaConfig = convertDependenciesToMetadataConfig(dependencies);

      if(currentCmdDeps !== JSON.stringify(depsMetaConfig)) {
        cmdMeta.dependencies = depsMetaConfig;
        graph.changeNodeMetadata(props.ctx.command, JSON.stringify(cmdMeta, null, 2));
        command = props.ctx.command.changeMetadata(JSON.stringify(cmdMeta, null, 2));
      }
    }

    if(!aggregate) {
      shouldRepositionFeature = true;
    }

    if(shouldRepositionFeature && props.ctx.feature) {
      const repositionedFeature = repositionFeature(
        props.ctx.feature,
        dddActionFromNode(graph.getNode(changedOrNewAggregate.getId())!, props.ctx.board),
        graph
      );

      graph.resizeNode(repositionedFeature, repositionedFeature.getSize());
    }

    resetForm();
    return {...props.ctx, command, aggregate: changedOrNewAggregate, commandDependencies: dependencies};
  });

  const nodeOptions = aggregate ? List.of(aggregate) : newAggregate ? List.of(newAggregate) : elementsTree
    .getFilteredElementsByType(NodeType.aggregate, true);

  const depsOptions = elementsTree.getFilteredElementsByTypes([NodeType.document, NodeType.externalSystem]);
  const cmdSchema = props.ctx.command ? getCommandMetadata(props.ctx.command).schema || new Schema({}) : new Schema({});

  return <form className='ui form' onSubmit={handleSubmit}>
    {props.mode === 'modal' && <div className={'field'}>
      {!showNewAggregateInput && aggregate &&
          <ChangeNodeNameOrDeleteNode node={aggregate} board={props.ctx.board} onNameChanged={handleNameChanged}
                                      onNodeDeleted={handleAggregateDeleted}/>}
      {!showNewAggregateInput && !aggregate &&
          <NodeSelect nodeOptions={nodeOptions} selected={selectedAggregate} onChange={handleAggregateSelection}
                      placeholder={props.t('insp.cody_wizard.node_select.select_aggregate') as string}/>}
      {showNewAggregateInput &&
          <NewNodeInput board={props.ctx.board} nameSuggestion={suggestAggregateName(props.ctx, graph)}
                        nodeType={NodeType.aggregate} onEntered={handleNewAggregateCreated}
                        onCancel={() => setShowNewAggregateInput(false)}/>}
      {<Button basic={true} icon="add" labelPosition="left" size="mini"
               content={props.t('insp.cody_wizard.step_aggregate.new_aggregate') as string} color="blue"
               className="text"
               onClick={() => setShowNewAggregateInput(true)}
               disabled={!!selectedAggregate || !!newAggregate || showNewAggregateInput}/>}
    </div>}
    <div className={'field'}>
      {<Button basic={true} icon={showDependencies? 'minus' : 'add'} labelPosition="left" size="mini"
               content={props.t('insp.cody_wizard.step_aggregate.manage_dependencies') + ` (${Object.keys(dependencies).length})`} color="blue" className="text"
               onClick={() => setShowDependencies(!showDependencies)}
               disabled={!selectedAggregate}/>}
    </div>
    <div className={(showDependencies? 'slidedown' : 'slideup') + ' smooth'} >
      <EditorDivider content={props.t('insp.cody_wizard.step_aggregate.dependencies_label') as string} />
      <MultipleNodeSelect selected={List(Object.values(dependencies).map(dep => dep.node))}
                          nodeOptions={depsOptions}
                          placeholder={props.t('insp.cody_wizard.step_aggregate.dependencies_placeholder') as string}
                          withNs={true}
                          uniqueName={true}
                          onChange={handleDependenciesChanged}
                          disabled={!selectedAggregate} />
      <DependenciesEditor boardId={props.ctx.board.uid} dependencies={dependencies} messageSchema={cmdSchema} autofocus={false} onDependenciesChanged={handleDependenciesConfigChanged} />

    </div>
    <EditorDivider content={props.t('insp.cody_wizard.step_aggregate.rules_label') as string} />
    <BusinessRulesEditor rules={metadata.rules || suggestEventRecordingRules(props.ctx)}
                         dependencies={dependencies}
                         onRulesChanged={handleRulesChanged}
                         autofocus={autoFocusBusinessRules}
                         possibleEvents={props.ctx.events}
    />
  </form>

};

export default withNamespaces()(Aggregate);

export const suggestEventRecordingRules = (ctx: ImmutableDDDWizardContext): Rule[] => {
  const rules: Rule[] = [];

  ctx.events.forEach(evt => {
    rules.push({
      rule: "always",
      then: {
        record: {
          event: evt.getName(),
          mapping: "command"
        }
      }
    })
  })

  return rules;
}

export const suggestAggregateName = (ctx: ImmutableDDDWizardContext, graph: Graph): string | undefined => {
  let name = '';

  if(ctx.state) {
    return ctx.state.getName();
  } else if(ctx.command) {
    name = ctx.command.getName();
  } else if(ctx.events.count()) {
    name = ctx.events.first(makeNodeFromJs({})).getName();
  }

  if(name === '') {
    return;
  }

  const nodeNames = names(name);

  const words = tagSentence(nodeNames.title);

  console.log("[CodyWizard] tagged words: ", words);

  return words.filter(word => isNoun(word) || isAdjective(word)).map(word =>  isNoun(word) ? ensureSingularNoun(word) : word.token).join(" ");
}

const updateDependenciesOnGraph = (aggregate: Node, ctx: ImmutableDDDWizardContext, dependencies: List<Node>, graph: Graph): boolean => {
  const existingDependencies = List(Object.values(ctx.commandDependencies || {}).map(dep => dep.node));
  const existingDependencyIds = existingDependencies.map(dep => dep.getId());
  const changedDependencyIds = dependencies.map(vm => vm.getId());

  const dependenciesToDelete = existingDependencies.filter(dep => !changedDependencyIds.contains(dep.getId()));
  const dependenciesToAdd = dependencies.filter(vm => !existingDependencyIds.contains(vm.getId()));

  dependenciesToDelete.forEach(vm => graph.deleteNode(vm));

  const lastExistingDependency = existingDependencies.last(undefined);
  let anchorNode = lastExistingDependency || null;
  let position = NextToPosition.below;
  if(!anchorNode) {
    anchorNode = aggregate;
    position = NextToPosition.left;
  }

  dependenciesToAdd.forEach(vm => {
    const newDependency = makeNodeFromJs({
      id: graph.createCellId(),
      type: vm.getType(),
      name: vm.getName(),
      metadata: vm.getMetadata(),
    });

    graph.addNodeNextToAnother(newDependency, anchorNode as Node, position, DEFAULT_CONNECT_MARGIN);
    graph.connectNodes(newDependency, aggregate);
    markAsConnected(newDependency, graph);
    anchorNode = graph.getNode(newDependency.getId());
    position = NextToPosition.below;
  })

  return dependenciesToAdd.count() > 0 || dependenciesToDelete.count() > 0;
}
