import * as React from 'react';
import {useState} from "react";
import {withNamespaces, WithNamespaces} from "react-i18next";
import {Button, FormField} from "semantic-ui-react";
import EditableTextline from "../../../../core/components/EditableTextline";
import {useElementsTree} from "../../../hooks/useElementsTree";
import {useGraph} from "../../../hooks/useGraph";
import {Board} from "../../../model/Board";
import {Node} from "../../../model/Graph";
import {ADDITIONAL_CHAR_PADDING, CHAR_SIZE_UNIT, INPUT_CHAR_SIZE} from "../WizardModal";

interface OwnProps {
  node: Node;
  board: Board;
  onNameChanged: (name: string) => void;
  onNodeDeleted?: () => void;
  disableDelete?: boolean;
  uniqueName?: boolean;
}

type ChangeNodeNameOrDeleteNodeProps = OwnProps & WithNamespaces;

const ChangeNodeNameOrDeleteNode = (props: ChangeNodeNameOrDeleteNodeProps) => {

  const [graph,] = useGraph();
  const elementsTree = useElementsTree(props.board.uid);

  const handleDeleteNode = () => {
    graph.deleteNode(props.node);
    if(props.onNodeDeleted) {
      props.onNodeDeleted();
    }
  }

  const validate = (name: string): boolean | string => {
    if(props.uniqueName) {
      const unique = elementsTree.getSimilarElements(props.node.changeName(name.trim())).count() === 0;

      if(!unique) {
        return props.t('insp.cody_wizard.node_select.unique_name_error') as string;
      }
    }

    return true;
  }

  const handleNameChange = (newName: string) => {
    props.onNameChanged(newName.trim());
  }

 return <FormField>
   {!props.disableDelete && <Button floated="right" icon="trash alternate outline" basic={true} color="red" className="noborder"
            onClick={handleDeleteNode}/>}
   <EditableTextline text={props.node.getName()}
                     required={true}
                     minWidth={20}
                     charSize={INPUT_CHAR_SIZE}
                     widthUnit={CHAR_SIZE_UNIT}
                     padding={ADDITIONAL_CHAR_PADDING}
                     onTextChanged={handleNameChange}
                     focus={true}
                     validate={validate}
   />
 </FormField>
};

export default withNamespaces()(ChangeNodeNameOrDeleteNode);
