import {NodeType} from "../../../model/Graph";
import {RulesDependenciesMetadata, RulesDependency} from "./types";

export const convertDependenciesToMetadataConfig = (dependencies: Record<string, RulesDependency>): RulesDependenciesMetadata | undefined => {
  const metadata: RulesDependenciesMetadata = {};

  // tslint:disable-next-line:forin
  for (const depKey in dependencies) {
    metadata[depKey] = {
      type: dependencies[depKey].node.getType() === NodeType.document ? 'query' : 'service',
      alias: dependencies[depKey].alias,
      options: dependencies[depKey].options,
    }
  }

  return Object.values(metadata).length > 0 ? metadata : undefined;
}
