declare const inspectioUtils: {getTypeInclEdge: (cell: any) => HistoryCellType | false, getLabelText: (cell: any) => string, isContainer: (cell: any) => boolean, getMetadata: (cell: any) => string | undefined, getTags: (cell: any) => string[]};
