import {List} from "immutable";
import * as React from 'react';
import {FormEvent, useEffect, useState} from 'react';
import {withNamespaces, WithNamespaces} from "react-i18next";
import {Button, ButtonGroup} from "semantic-ui-react";
import {useElementsTree} from "../../../hooks/useElementsTree";
import {useGraph} from "../../../hooks/useGraph";
import {ElementsTree} from "../../../model/ElementsTree";
import {convertNodeToJs, makeNodeFromJs, NextToPosition, Node, NodeMetadata, NodeType} from "../../../model/Graph";
import {CommandMetadata, getCommandMetadata} from "../../../service/cody-wizard/command/get-command-metadata";
import {dddActionFromNode, ImmutableDDDWizardContext} from "../../../service/cody-wizard/context/ddd-action";
import {updateContext} from "../../../service/cody-wizard/context/wizard-context";
import {getAggregateEventMetadata} from "../../../service/cody-wizard/event/get-aggregate-event-metadata";
import {markAsConnected} from "../../../service/cody-wizard/node/mark-as-connected";
import {names} from "../../../service/cody-wizard/node/names";
import {
  convertDependenciesToMetadataConfig
} from "../../../service/cody-wizard/rule-engine/convert-dependencies-to-metadata-config";
import { Schema } from "../../../service/cody-wizard/schema/schema";
import {
  ensurePresentTense,
  isVerb,
  tagSentence
} from "../../../service/cody-wizard/tagger";
import {UiSchema} from "../../../service/cody-wizard/ui/ui-schema";
import {getVoMetadata} from "../../../service/cody-wizard/vo/get-vo-metadata";
import EditorDivider from "../Editor/EditorDivider";
import SchemaEditor from "../Editor/SchemaEditor";
import UiSchemaEditor from "../Editor/UiSchemaEditor";
import ChangeNodeNameOrDeleteNode from "../Node/ChangeNodeNameOrDeleteNode";
import NewNodeInput from "../Node/NewNodeInput";
import NodeSelect from "../Node/NodeSelect";
import {DEFAULT_CONNECT_MARGIN, WithWizardStepProps} from "../WizardModal";
import DeleteHistoryToggle from "./AggregateCommand/DeleteHistoryToggle";
import DeleteStateToggle from "./AggregateCommand/DeleteStateToggle";
import NewAggregateToggle from "./AggregateCommand/NewAggregateToggle";
import {repositionFeature} from "./Feature";
import {AggregateMetadata} from "../../../service/cody-wizard/aggregate/get-aggregate-metadata";

interface OwnProps {}

type AggregateCommandProps = OwnProps & WithWizardStepProps<ImmutableDDDWizardContext> & WithNamespaces;

const StateVO = (props: AggregateCommandProps) => {
  const [graph] = useGraph();
  const elementsTree = useElementsTree(props.ctx.board.uid);
  const [selectedCommand, setSelectedCommand] = useState<Node|null>(null);
  const [newCommand, setNewCommand] = useState<Node|null>(null);
  const [showNewCommandInput, setShowNewCommandInput] = useState(false);
  const [metadata, setMetadata] = useState<CommandMetadata>({});
  const [autoFocusSchema, setAutoFocusSchema] = useState(false);
  const [nodeName, setNodeName] = useState(props.ctx.command? props.ctx.command.getName() : '');

  const updateMetadata = (meta: CommandMetadata) => {
    if(props.onUpdateMetadata) {
      props.onUpdateMetadata(JSON.stringify(meta, null, 2), true);
    }

    setMetadata(meta);
  }

  useEffect(() => {
    resetForm();

    if(props.ctx.command) {
      setSelectedCommand(props.ctx.command);
      const commandMeta = getCommandMetadata(props.ctx.command);

      if(props.ctx.commandDependencies) {
        commandMeta.dependencies = convertDependenciesToMetadataConfig(props.ctx.commandDependencies);
      }

      setMetadata(commandMeta);
      setNodeName(props.ctx.command.getName());
    } else {
      setShowNewCommandInput(true);
    }

  }, [props.ctx.command])

  const resetForm = () => {
    setSelectedCommand(null);
    setNewCommand(null);
    setMetadata({});
    setAutoFocusSchema(false);
    setNodeName('');
  }

  const handleSubmit = (evt: FormEvent) => {
    evt.preventDefault();
    evt.stopPropagation();
  }

  const handleCommandSelection = (selection: Node | null) => {
    // Also clear new state in this case
    if(!selection) {
      resetForm();
    } else if (!selectedCommand || selection.getId() !== selectedCommand.getId()) {
      setSelectedCommand(selection);
      const selectedMeta = getCommandMetadata(selection);

      if(props.ctx.commandDependencies) {
        selectedMeta.dependencies = convertDependenciesToMetadataConfig(props.ctx.commandDependencies);
      }

      setMetadata(selectedMeta);
    }
  }

  const handleNewCommandCreated = (createdCommand: Node) => {
    const newAggregate = suggestCommandCreatesNewAggregate(props.ctx, elementsTree);

    const schema = suggestAggregateCommandSchema(props.ctx, newAggregate);

    setShowNewCommandInput(false);
    setSelectedCommand(createdCommand);
    setNewCommand(createdCommand);
    setMetadata({
      schema,
      uiSchema: suggestAggregateCommandUiSchema(props.ctx),
      newAggregate,
      dependencies: convertDependenciesToMetadataConfig(props.ctx.commandDependencies),
    });
    setAutoFocusSchema(true);
  }

  const handleNameChanged = (newNodeName: string) => {
    if(props.ctx.command) {
      graph.changeNodeName(props.ctx.command, newNodeName);
      props.onCtxChanged(updateContext(props.ctx, {command: props.ctx.command.changeName(newNodeName)}))
    }
  }

  const handleCommandDeleted = () => {
    props.onCtxChanged(updateContext(props.ctx, {command: null}));
  }

  const handleNewAggregateChanged = (newAggregate: boolean) => {
    updateMetadata({...metadata, newAggregate});
  }

  const handleDeleteStateChanged = (deleteState: boolean) => {
    updateMetadata({...metadata, deleteState, deleteHistory: false});
  }

  const handleDeleteHistoryChanged = (deleteHistory: boolean) => {
    updateMetadata({...metadata, deleteHistory});
  }

  const handleSchemaChanged = (newSchema: Schema) => {
    updateMetadata({...metadata, schema: newSchema});
  }

  const handleUiSchemaChanged = (newUiSchema: UiSchema) => {
    updateMetadata({...metadata, uiSchema: newUiSchema});
  }

  const handleCopyStateSchema = () => {
    if(props.ctx.state) {
      const stateMeta = getVoMetadata(props.ctx.state);

      if(stateMeta.schema) {
        handleSchemaChanged(stateMeta.schema);
        return;
      }
    }

    handleSchemaChanged(new Schema({}));
  }

  const handleCopyStateUiSchema = () => {
    if(props.ctx.state) {
      const stateMeta = getVoMetadata(props.ctx.state);

      if(stateMeta.uiSchema) {
        const uiSchema = {
          ...(metadata.uiSchema && metadata.uiSchema["ui:button"]? {"ui:button": metadata.uiSchema["ui:button"]} : {}),
          ...stateMeta.uiSchema
        };
        handleUiSchemaChanged(uiSchema as UiSchema);
        return;
      }
    }

    handleUiSchemaChanged({});
  }

  props.onFinish((): ImmutableDDDWizardContext => {
    if(!selectedCommand) {
      resetForm();
      return props.ctx;
    }

    let changedOrNewCommand = props.ctx.command
      ? makeNodeFromJs({...convertNodeToJs(props.ctx.command), ...{name: selectedCommand.getName(), metadata: JSON.stringify(metadata, null, 2)}})
      : makeNodeFromJs({
        id: graph.createCellId(),
        type: selectedCommand.getType(),
        name: selectedCommand.getName(),
        metadata: JSON.stringify(metadata, null, 2),
      })

    if(!props.ctx.command) {
      const aggregate = props.ctx.aggregate;

      if(!aggregate) {
        console.error("[CodyWizard] Unable to create Command. An aggregate is missing to connect the command card to.");
        return props.ctx;
      }

      graph.addNodeNextToAnother(changedOrNewCommand, aggregate, NextToPosition.above, DEFAULT_CONNECT_MARGIN);
      graph.connectNodes(changedOrNewCommand, aggregate);
      changedOrNewCommand = markAsConnected(changedOrNewCommand, graph);

      if(props.ctx.feature) {
        const repositionedFeature = repositionFeature(
          props.ctx.feature,
          dddActionFromNode(graph.getNode(changedOrNewCommand.getId())!, props.ctx.board),
          graph
        )

        graph.resizeNode(repositionedFeature, repositionedFeature.getSize());
      }
    }

    graph.changeNodeMetadata(changedOrNewCommand, changedOrNewCommand.getMetadata() as NodeMetadata);

    resetForm();
    return {...props.ctx, command: changedOrNewCommand};
  })


  const nodeOptions = props.ctx.command ? List.of(props.ctx.command) : newCommand ? List.of(newCommand) : List([]);

  return <form className='ui form' onSubmit={handleSubmit}>
    {props.mode === 'modal' && <div className={'field'}>
      {!showNewCommandInput && props.ctx.command &&
          <ChangeNodeNameOrDeleteNode node={props.ctx.command} board={props.ctx.board} uniqueName={true}
                                      onNameChanged={handleNameChanged} onNodeDeleted={handleCommandDeleted}/>}
      {!showNewCommandInput && !props.ctx.command &&
          <NodeSelect nodeOptions={nodeOptions} selected={selectedCommand} onChange={handleCommandSelection}
                      placeholder={props.t('insp.cody_wizard.node_select.select_command') as string}/>}
      {showNewCommandInput &&
          <NewNodeInput board={props.ctx.board} uniqueName={true} nameSuggestion={suggestNewCommandName(props.ctx)}
                        nodeType={NodeType.command} onEntered={handleNewCommandCreated}
                        onCancel={() => setShowNewCommandInput(false)}/>}
      {<Button basic={true} icon="add" labelPosition="left" size="mini"
               content={props.t('insp.cody_wizard.step_command.new_command') as string} color="blue" className="text"
               onClick={() => setShowNewCommandInput(true)}
               disabled={!!selectedCommand || !!newCommand || showNewCommandInput}/>}
    </div>}
    <NewAggregateToggle command={props.ctx.command || newCommand} ctx={props.ctx} newAggregate={metadata.newAggregate} deleteState={metadata.deleteState} onNewAggregateChanged={handleNewAggregateChanged} />
    <DeleteStateToggle command={props.ctx.command || newCommand} deleteState={metadata.deleteState} newAggregate={metadata.newAggregate} onDeleteStateChanged={handleDeleteStateChanged} />
    <DeleteHistoryToggle command={props.ctx.command || newCommand} deleteHistory={metadata.deleteHistory} deleteState={metadata.deleteState} onDeleteHistoryChanged={handleDeleteHistoryChanged} />
    <EditorDivider content="Schema" />
    <SchemaEditor boardId={props.ctx.board.uid} schema={metadata.schema || Schema.fromString('{}')} onSchemaChanged={handleSchemaChanged} autofocus={autoFocusSchema} />
    <Button basic={true} compact={true} icon="arrow alternate circle up" className="noborder" content={props.t('insp.cody_wizard.schema_editor.copy_state_schema')} disabled={!props.ctx.state} onClick={handleCopyStateSchema} />
    <EditorDivider content="UI Schema" />
    <UiSchemaEditor boardId={props.ctx.board.uid} nodeSchema={metadata.schema || Schema.fromString('{}')} schema={metadata.uiSchema || {}} onSchemaChanged={handleUiSchemaChanged} buttonSchema={true} />
    <ButtonGroup>
      <Button basic={true} compact={true} icon="arrow alternate circle up" className="noborder" content={props.t('insp.cody_wizard.step_command.copy_state_ui_schema')} disabled={!props.ctx.state} onClick={handleCopyStateUiSchema} />
      <Button basic={true} compact={true} icon="search" className="noborder" content={props.t('insp.cody_wizard.ui_schema_editor.choose_icon')} as="a" href="https://pictogrammers.com/library/mdi/" target="_blank" />
    </ButtonGroup>

    <div style={{marginTop: '100px'}} />
  </form>
};

export default withNamespaces()(StateVO);

export const suggestAggregateCommandSchema = (ctx: ImmutableDDDWizardContext, newAggregate: boolean): Schema => {
  const firstEvent = ctx.events.first(undefined);
  const state = ctx.state;
  if(newAggregate && state) {
    const stateMeta = getVoMetadata(state);

    if(stateMeta.schema) {
      return stateMeta.schema;
    }
  }

  if(firstEvent) {
    const evtMeta = getAggregateEventMetadata(firstEvent);

    if(evtMeta.schema) {
      return evtMeta.schema;
    }
  }

  if(state) {
    const stateMetaAgain = getVoMetadata(state);

    if(stateMetaAgain.identifier) {
      const rawSchema: Record<string, any> = {};
      rawSchema[stateMetaAgain.identifier] = 'string|format:uuid';
      return new Schema(rawSchema)
    }
  }

  return new Schema({});
}

export const suggestAggregateCommandUiSchema = (ctx: ImmutableDDDWizardContext): UiSchema => {
  if(ctx.state) {
    const stateMeta = getVoMetadata(ctx.state);

    if(stateMeta.uiSchema) {
      return stateMeta.uiSchema;
    }

    if(stateMeta.identifier) {
      const uiSchema: Record<string, any> = {};
      uiSchema[stateMeta.identifier] = {
        'ui:widget': 'hidden'
      }

      return uiSchema;
    }
  }

  return {};
}

export const suggestNewCommandName = (ctx: ImmutableDDDWizardContext): string | undefined => {
  const name = '';

  if(ctx.events.first(undefined)) {
    const eventName = ctx.events.first(makeNodeFromJs({})).getName();
    const eventNameTitle = names(eventName).title;

    const words = tagSentence(eventNameTitle);

    console.log("[CodyWizard] tagged words: ", words);

    const rest = words.filter(w => !isVerb(w)).map(w => w.token);
    const verbs = words.filter(w => isVerb(w)).map(w => ensurePresentTense(w));

    return verbs.join(" ") + " " + rest.join(" ");
  }

  return name;

  // @TODO check if new ar or update command, suggest name based on state/ar name + Add or Update
}

export const suggestCommandCreatesNewAggregate = (ctx: ImmutableDDDWizardContext, elementsTree: ElementsTree): boolean => {
  if(ctx.aggregate) {
    const similarAggregates = elementsTree.getFilteredElementsByTypeAndLabel(NodeType.aggregate, ctx.aggregate.getName())
      .filter(ele => ele.getId() !== ctx.aggregate!.getId());

    return similarAggregates.count() === 0;
  }

  if(ctx.state) {
    const similarStates = elementsTree.getFilteredElementsByTypeAndLabel(NodeType.document, ctx.state.getName())
      .filter(ele => ele.getId() !== ctx.state!.getId());

    return similarStates.count() === 0;
  }

  return true;
}
