import {Node, NodeType} from "../../../model/Graph";
import {WizardContext} from "../context/wizard-context";
import {fqcn} from "../node/fqcn";
import {names} from "../node/names";

export const getDependencyName = (node: Node, ctx: WizardContext): string => {
  if(node.getType() === NodeType.document) {
    return fqcn(node, ctx.board.codyEngineServiceName(), true);
  }

  if(node.getType() === NodeType.externalSystem) {
    return names(node.getName()).className;
  }

  throw new Error(`Unsupported node type "${node.getType()}" given for a dependency`);
}
