import * as React from 'react';
import {useEffect, useRef, useState} from "react";
import {withNamespaces, WithNamespaces} from "react-i18next";
import {FormField, Message} from "semantic-ui-react";
import CodeEditor from "../../../../core/components/CodeEditor/CodeEditor";
import {UiMetadata} from "../../../service/cody-wizard/ui/get-ui-metadata";
import {ElementsTree} from "../../../model/ElementsTree";
import {NodeType} from "../../../model/Graph";
import {isStateVo} from "../../../service/cody-wizard/vo/is-state-vo";
import {getSchemaRefName} from "../../../service/cody-wizard/vo/get-schema-ref-name";
import {getVoMetadata} from "../../../service/cody-wizard/vo/get-vo-metadata";
import {makeUiConfigSchema} from "./Validation/ui-config";
import {useElementsTree} from "../../../hooks/useElementsTree";
import {BoardId} from "../../../model/Board";


interface OwnProps {
  boardId: BoardId;
  uiConfig: UiMetadata;
  onUiConfigChanged: (config: UiMetadata) => void;
  autofocus?: boolean;
}

type UiConfigEditorProps = OwnProps & WithNamespaces;

const UiConfigEditor = (props: UiConfigEditorProps) => {
  const elementsTree = useElementsTree(props.boardId);
  const codeEditorRef = useRef<CodeEditor|null>(null);
  const [editorHasFocus, setEditorHasFocus] = useState(false);
  const [invalidJson, setInvalidJson] = useState(false);

  useEffect(() => {
    if (!codeEditorRef.current) {
      return;
    }

    codeEditorRef.current!.initializeModel({
      fileId: `ui-config.json`,
      language: "json",
      value: JSON.stringify(props.uiConfig, null, 2),
      schema: makeUiConfigSchema(getPossibleStateRefs(elementsTree)),
    });

    setInvalidJson(false);
  }, [props.uiConfig]);

  useEffect(() => {
    if(props.autofocus && codeEditorRef.current) {
      codeEditorRef.current.focus();
    }
  }, [props.autofocus])

  const propagateChanges = (editorStr: string, silentError?: boolean) => {
    try {
      const changedConfig = JSON.parse(editorStr);
      props.onUiConfigChanged(changedConfig);
    } catch (e) {
      console.error("[CodyWizard] UI Config Editor invalid JSON: ", e);
      if(!silentError) {
        setInvalidJson(true);
      }
    }
  }

  return <FormField>
    {invalidJson && <Message error={true} size="small" content={props.t('insp.cody_wizard.schema_editor.invalid_json') as string} icon="warning" style={{display: 'flex'}}/>}
    <CodeEditor
      containerId={'cody-ui-config-editor'}
      ref={codeEditorRef}
      options={{
        fontSize: 12,
        folding: true,
        glyphMargin: false,
        lineNumbers: true,
        lineDecorationsWidth: 3,
        minimap: {
          enabled: false
        },
        formatOnPaste: true,
        scrollBeyondLastLine: false,
        automaticLayout: true,
        scrollbar: {
          alwaysConsumeMouseWheel: false
        }
      }}
      className={"code editor" + (editorHasFocus? ' focus' : '')}
      onBlur={() => {
        if(codeEditorRef.current) {
          const changedUiConfigsStr = codeEditorRef.current.retrievePayload();

          setEditorHasFocus(false);

          propagateChanges(changedUiConfigsStr);
        }
      }}
      onFocus={() => {
        setEditorHasFocus(true);
      }}
    />
  </FormField>
};

export default withNamespaces()(UiConfigEditor);

const getPossibleStateRefs = (elementsTree: ElementsTree): string[] => {
  return elementsTree.getFilteredElementsByType(NodeType.document)
    .filter(vo => isStateVo(vo))
    .map(vo => {
      const voMeta = getVoMetadata(vo);
      return getSchemaRefName(vo, voMeta);
    })
    .toArray()
}
