import {List} from "immutable";
import * as React from 'react';
import {FormEvent, useEffect, useState} from 'react';
import {withNamespaces, WithNamespaces} from "react-i18next";
import {Button, ButtonGroup} from "semantic-ui-react";
import {useElementsTree} from "../../../hooks/useElementsTree";
import {useGraph} from "../../../hooks/useGraph";
import {
  convertNodeToJs,
  Graph,
  makeNodeFromJs,
  NextToPosition,
  Node,
  NodeMetadata,
  NodeType
} from "../../../model/Graph";
import {getCommandMetadata} from "../../../service/cody-wizard/command/get-command-metadata";
import {dddActionFromNode, ImmutableDDDWizardContext} from "../../../service/cody-wizard/context/ddd-action";
import {updateContext} from "../../../service/cody-wizard/context/wizard-context";
import {getAggregateEventMetadata} from "../../../service/cody-wizard/event/get-aggregate-event-metadata";
import {markAsConnected} from "../../../service/cody-wizard/node/mark-as-connected";
import {names} from "../../../service/cody-wizard/node/names";
import {Rule} from "../../../service/cody-wizard/rule-engine/configuration";
import {Schema} from "../../../service/cody-wizard/schema/schema";
import {ShorthandObject} from "../../../service/cody-wizard/schema/shorthand";
import {ensureSingularNoun, isAdjective, isNoun, tagSentence} from "../../../service/cody-wizard/tagger";
import {UiSchema} from "../../../service/cody-wizard/ui/ui-schema";
import {getDefaultUiTableSchema} from "../../../service/cody-wizard/vo/get-default-ui-table-schema";
import {getSchemaRefName} from "../../../service/cody-wizard/vo/get-schema-ref-name";
import {getVoMetadata, ValueObjectMetadata} from "../../../service/cody-wizard/vo/get-vo-metadata";
import {isListVo} from "../../../service/cody-wizard/vo/is-list-vo";
import {isStateVo} from "../../../service/cody-wizard/vo/is-state-vo";
import {isStateVoCandidate} from "../../../service/cody-wizard/vo/is-state-vo-candidate";
import EditorDivider from "../Editor/EditorDivider";
import InitializeRulesEditor from "../Editor/InitializeRulesEditor";
import QuerySchemaEditor from "../Editor/QuerySchemaEditor";
import SchemaEditor from "../Editor/SchemaEditor";
import UiSchemaEditor from "../Editor/UiSchemaEditor";
import ChangeNodeNameOrDeleteNode from "../Node/ChangeNodeNameOrDeleteNode";
import NewNodeInput from "../Node/NewNodeInput";
import NodeSelect from "../Node/NodeSelect";
import {DEFAULT_CONNECT_MARGIN, WithWizardStepProps} from "../WizardModal";
import AdvancedButton from "./AdvancedButton";
import {repositionFeature} from "./Feature";
import IdentifierSelect from "./StateVO/IdentifierSelect";
import NamespaceInput from "./StateVO/NamespaceInput";
import {ElementsTree} from "../../../model/ElementsTree";

interface OwnProps {}

type VOProps = OwnProps & WithWizardStepProps<ImmutableDDDWizardContext> & WithNamespaces;

const StateVO = (props: VOProps) => {
  const [graph] = useGraph();
  const elementsTree = useElementsTree(props.ctx.board.uid);
  const [selectedState, setSelectedState] = useState<Node|null>(null);
  const [newState, setNewState] = useState<Node|null>(null);
  const [showNewStateInput, setShowNewStateInput] = useState(false);
  const [metadata, setMetadata] = useState<ValueObjectMetadata>({});
  const [autoFocusSchema, setAutoFocusSchema] = useState(false);
  const [nodeName, setNodeName] = useState(props.ctx.state? props.ctx.state.getName() : '');
  const [showAdvancedSettings, setShowAdvancedSettings] = useState(false);

  const updateMetadata = (meta: ValueObjectMetadata) => {
    if(props.onUpdateMetadata) {
      props.onUpdateMetadata(JSON.stringify(meta, null, 2), true);
    }

    setMetadata(meta);
  }

  useEffect(() => {
    resetForm();

    if(props.ctx.state) {
      setSelectedState(props.ctx.state);
      const stateMeta = getVoMetadata(props.ctx.state);

      if(!stateMeta.schema || stateMeta.schema.isEmpty()) {
        stateMeta.schema = suggestStateSchema(props.ctx);
      }

      setMetadata(stateMeta);
      setNodeName(props.ctx.state.getName());
    }

  }, [props.ctx.state])

  const resetForm = () => {
    setSelectedState(null);
    setNewState(null);
    setMetadata({});
    setAutoFocusSchema(false);
    setNodeName('');
  }

  const handleSubmit = (evt: FormEvent) => {
    evt.preventDefault();
    evt.stopPropagation();
  }

  const handleDataEntitySelection = (selection: Node | null) => {
    // Also clear new state in this case
    if(!selection) {
      resetForm();
    } else if (!selectedState || selection.getId() !== selectedState.getId()) {
      setSelectedState(selection);
      const selectedMeta = getVoMetadata(selection);

      if(!selectedMeta.schema || selectedMeta.schema.isEmpty()) {
        selectedMeta.schema = suggestStateSchema(props.ctx);
      }

      setMetadata(selectedMeta);
    }
  }

  const handleNewDataEntityCreated = (createdEntity: Node) => {
    const voNames = names(createdEntity.getName());
    const rawSchema: ShorthandObject = {};
    const rawQuerySchema: ShorthandObject = {};
    const uiSchema: UiSchema = {};
    const identifier = voNames.propertyName + 'Id';
    rawSchema[identifier] = 'string|format:uuid';
    rawQuerySchema[identifier] = 'string|format:uuid';
    uiSchema[identifier] = {
      "ui:widget": "hidden"
    };

    const suggestedSchema = suggestStateSchema(props.ctx);

    setShowNewStateInput(false);
    setSelectedState(createdEntity);
    setNewState(createdEntity);
    setMetadata({
      ns: '/' + names(createdEntity.getName()).className,
      schema: !suggestedSchema.isEmpty() ? suggestedSchema : new Schema(rawSchema),
      querySchema: new Schema(rawQuerySchema),
      uiSchema,
      identifier,
    });
    setAutoFocusSchema(true);
  }

  const handleNameChanged = (newNodeName: string) => {
    if(props.ctx.state) {
      graph.changeNodeName(props.ctx.state, newNodeName);
      props.onCtxChanged(updateContext(props.ctx, {state: props.ctx.state.changeName(newNodeName)}))
    }
  }

  const handleStateDeleted = () => {
    props.onCtxChanged(updateContext(props.ctx, {state: null}));
  }

  const handleNsChanged = (newNs: string) => {
    updateMetadata({...metadata, ns: newNs});
  }

  const handleIdentifierChanged = (newIdentifier: string) => {
    updateMetadata({...metadata, identifier: newIdentifier});
  }

  const handleSchemaChanged = (newSchema: Schema) => {
    updateMetadata({...metadata, schema: newSchema});
  }

  const handleQuerySchemaChanged = (newQuerySchema: Schema) => {
    updateMetadata({...metadata, querySchema: newQuerySchema});
  }

  const handleUiSchemaChanged = (newUiSchema: UiSchema) => {
    updateMetadata({...metadata, uiSchema: newUiSchema});
  }

  const handleCopyCommandSchema = () => {
    if(props.ctx.command) {
      const commandMeta = getCommandMetadata(props.ctx.command);

      if(commandMeta.schema) {
        handleSchemaChanged(commandMeta.schema);
        return;
      }

      handleSchemaChanged(new Schema({}));
    }
  }

  const handleCopyUiSchemaFromCommand = () => {
    if(props.ctx.command) {
      const commandMeta = getCommandMetadata(props.ctx.command);

      if(commandMeta.uiSchema) {
        const uiSchema = {...commandMeta.uiSchema};
        delete uiSchema['ui:button'];
        handleUiSchemaChanged(uiSchema);
        return;
      }

      handleUiSchemaChanged({});
    }
  }

  const handleInitializeRulesChanged = (rules: Rule[]) => {
    updateMetadata({...metadata, initialize: rules});
  }

  props.onFinish((): ImmutableDDDWizardContext => {
    if(!selectedState) {
      resetForm();
      return props.ctx;
    }

    let changedOrNewState = props.ctx.state
      ? makeNodeFromJs({...convertNodeToJs(props.ctx.state), ...{name: selectedState.getName(), metadata: JSON.stringify(metadata, null, 2)}})
      : makeNodeFromJs({
      id: props.ctx.state ? selectedState.getId() : graph.createCellId(),
      type: selectedState.getType(),
      name: selectedState.getName(),
      metadata: JSON.stringify(metadata, null, 2),
    })

    if(!props.ctx.state) {
      const firstEvent = props.ctx.events.first(undefined);

      if(!firstEvent) {
        console.error("[CodyWizard] Unable to create Data Entity. An event is missing to connect the information card to.");
        return props.ctx;
      }

      graph.addNodeNextToAnother(changedOrNewState, firstEvent, NextToPosition.below, DEFAULT_CONNECT_MARGIN);
      graph.connectNodes(firstEvent, changedOrNewState);
      changedOrNewState = markAsConnected(changedOrNewState, graph);
    }

    let listVo: Node | null = null;

    if(newState) {
      // Also create a corresponding list VO for new state
      listVo = createListFromState(changedOrNewState, metadata, graph, elementsTree);
      if(listVo) {
        graph.addNodeNextToAnother(listVo, changedOrNewState, NextToPosition.below, DEFAULT_CONNECT_MARGIN);
        graph.connectNodes(changedOrNewState, listVo);
        listVo = markAsConnected(listVo, graph);
      }

      if(props.ctx.feature) {
        const repositionedFeature = repositionFeature(
          props.ctx.feature,
          dddActionFromNode(graph.getNode(changedOrNewState.getId())!, props.ctx.board),
          graph
        )

        graph.resizeNode(repositionedFeature, repositionedFeature.getSize());
      }
    }

    if(!listVo) {
      listVo = findStateListViewModel(changedOrNewState);
    }

    graph.changeNodeMetadata(changedOrNewState, changedOrNewState.getMetadata() as NodeMetadata);

    resetForm();
    return {...props.ctx, state: changedOrNewState, stateList: listVo};
  })


  const nodeOptions = props.ctx.state ? List.of(props.ctx.state) : newState ? List.of(newState) : elementsTree
    .getFilteredElementsByType(NodeType.document).filter(candidate => isStateVoCandidate(candidate));

  return <form className='ui form' onSubmit={handleSubmit}>
    {props.mode === 'modal' && <div className={'field'}>
      {!showNewStateInput && props.ctx.state &&
          <ChangeNodeNameOrDeleteNode node={props.ctx.state} board={props.ctx.board} onNameChanged={handleNameChanged}
                                      onNodeDeleted={handleStateDeleted}/>}
      {!showNewStateInput && !props.ctx.state &&
          <NodeSelect nodeOptions={nodeOptions} selected={selectedState} onChange={handleDataEntitySelection}
                      placeholder={props.t('insp.cody_wizard.node_select.select_data_entity') as string}
                      withNs={true}
                      uniqueName={true}
          />}
      {showNewStateInput && <NewNodeInput board={props.ctx.board} nameSuggestion={suggestNewEntityName(props.ctx)}
                                          nodeType={NodeType.document} onEntered={handleNewDataEntityCreated}
                                          onCancel={() => setShowNewStateInput(false)}/>}
      {<Button basic={true} icon="add" labelPosition="left" size="mini"
               content={props.t('insp.cody_wizard.step_state.new_data_entity') as string} color="blue" className="text"
               onClick={() => setShowNewStateInput(true)}
               disabled={!!selectedState || !!newState || showNewStateInput}/>}
    </div>}
    <EditorDivider content="Namespace" />
    <NamespaceInput ns={metadata.ns || ''} onNsChanged={handleNsChanged} />
    <EditorDivider content="Identifier" />
    <IdentifierSelect identifier={metadata.identifier || ''} schema={metadata.schema || Schema.fromString('{}')} onIdentifierChanged={handleIdentifierChanged} />
    <EditorDivider content="Schema" />
    <SchemaEditor boardId={props.ctx.board.uid} schema={metadata.schema || Schema.fromString('{}')} onSchemaChanged={handleSchemaChanged} autofocus={autoFocusSchema} />
    <Button basic={true} compact={true} icon="arrow alternate circle up" className="noborder" content={props.t('insp.cody_wizard.schema_editor.copy_command_schema')} disabled={!props.ctx.command} onClick={handleCopyCommandSchema} />
    <EditorDivider content="Query Schema" />
    <QuerySchemaEditor boardId={props.ctx.board.uid} schema={metadata.querySchema || Schema.fromString('{}')} onSchemaChanged={handleQuerySchemaChanged} />
    <EditorDivider content="UI Schema" />
    <UiSchemaEditor boardId={props.ctx.board.uid} nodeSchema={metadata.schema || Schema.fromString('{}')} schema={metadata.uiSchema || {}} onSchemaChanged={handleUiSchemaChanged} />
    <ButtonGroup>
      <Button basic={true} compact={true} icon="arrow alternate circle up" className="noborder" content={props.t('insp.cody_wizard.step_state.copy_command_ui_schema')} disabled={!props.ctx.command} onClick={handleCopyUiSchemaFromCommand} />
      <Button basic={true} compact={true} icon="search" className="noborder" content={props.t('insp.cody_wizard.ui_schema_editor.choose_icon')} as="a" href="https://pictogrammers.com/library/mdi/" target="_blank" />
    </ButtonGroup>
    <AdvancedButton showAdvancedSettings={showAdvancedSettings} onShowChanged={show => setShowAdvancedSettings(show)}  disabled={!selectedState} />
    <div className={(showAdvancedSettings? 'slidedown' : 'slideup') + ' smooth'}>
      <EditorDivider content="Initialize Rules" />
      <InitializeRulesEditor initializeRules={metadata.initialize || []} dataSchema={metadata.schema || Schema.fromString('{}')} ctx={props.ctx} onRulesChanged={handleInitializeRulesChanged} />
    </div>
    <div style={{marginTop: '100px'}} />
  </form>
};

export default withNamespaces()(StateVO);

export const createListFromState = (state: Node, meta: ValueObjectMetadata, graph: Graph, elementsTree: ElementsTree): Node | null => {
  if(!isStateVo(state)) {
    return null;
  }

  const ref = getSchemaRefName(state, meta);

  const similarElements = elementsTree.getSimilarElements(makeNodeFromJs({
    id: graph.createCellId(),
    type: NodeType.document,
    name: state.getName() + ' List',
    metadata: JSON.stringify({ns: meta.ns || '/',}, null, 2)
  }));


  const listMeta: ValueObjectMetadata = (similarElements.count() > 0)
    ? getVoMetadata(similarElements.first())
    : {
    ns: meta.ns || '/',
    schema: new Schema({
      $items: ref
    }),
    querySchema: new Schema({}),
    uiSchema: getDefaultUiTableSchema(state, meta)
  }

  return makeNodeFromJs({
    id: graph.createCellId(),
    name: state.getName() + ' List',
    type: NodeType.document,
    metadata: JSON.stringify(listMeta, null, 2),
  })
}

export const suggestStateSchema = (ctx: ImmutableDDDWizardContext): Schema => {
  const firstEvent = ctx.events.first(undefined);
  const command = ctx.command;

  if(command) {
    const cmdMeta = getCommandMetadata(command);

    if(cmdMeta.schema) {
      return cmdMeta.schema;
    }
  }

  if(firstEvent) {
    const evtMeta = getAggregateEventMetadata(firstEvent);

    if(evtMeta.schema) {
      return evtMeta.schema;
    }
  }

  return new Schema({});
}

export const suggestNewEntityName = (ctx: ImmutableDDDWizardContext): string | undefined => {
  let name = '';

  if(ctx.aggregate) {
    name = ctx.aggregate.getName();
  } else if(ctx.command) {
    name = ctx.command.getName();
  } else if(ctx.events.count()) {
    name = ctx.events.first(makeNodeFromJs({})).getName();
  }

  if(name === '') {
    return;
  }

  const nodeNames = names(name);

  const words = tagSentence(nodeNames.title);

  console.log("[CodyWizard] tagged words: ", words);

  return words.filter(word => isNoun(word) || isAdjective(word)).map(word =>  isNoun(word) ? ensureSingularNoun(word) : word.token).join(" ");
}

const findStateListViewModel = (state: Node): Node | null => {
  const stateMeta = getVoMetadata(state);
  const stateRef = getSchemaRefName(state, stateMeta);

  for (const target of state.getTargets().toArray()) {
    if(target.getType() === NodeType.document && isListVo(target)) {
      const listVoMeta = getVoMetadata(target);
      const itemsSchema = listVoMeta.schema!.getListItemsSchema(new Schema({}));

      if(itemsSchema.isRef() && itemsSchema.getRef() === stateRef) {
        return target;
      }
    }
  }

  return null;
}
