import * as React from 'react';
import {FormEvent, useEffect, useState} from "react";
import {withNamespaces, WithNamespaces} from "react-i18next";
import {useElementsTree} from "../../../hooks/useElementsTree";
import {useGraph} from "../../../hooks/useGraph";
import {convertNodeToJs, makeNodeFromJs, NextToPosition, Node, NodeMetadata} from "../../../model/Graph";
import {ImmutableInformationContext} from "../../../service/cody-wizard/context/information-context";
import {updateContext} from "../../../service/cody-wizard/context/wizard-context";
import {Schema} from "../../../service/cody-wizard/schema/schema";
import {UiSchema} from "../../../service/cody-wizard/ui/ui-schema";
import {getVoMetadata, ResolveConfig, ValueObjectMetadata} from "../../../service/cody-wizard/vo/get-vo-metadata";
import EditorDivider from "../Editor/EditorDivider";
import QuerySchemaEditor from "../Editor/QuerySchemaEditor";
import SchemaEditor from "../Editor/SchemaEditor";
import UiSchemaEditor from "../Editor/UiSchemaEditor";
import ChangeNodeNameOrDeleteNode from "../Node/ChangeNodeNameOrDeleteNode";
import {WithWizardStepProps} from "../WizardModal";
import NamespaceInput from "./StateVO/NamespaceInput";
import {Button, ButtonGroup} from "semantic-ui-react";
import {isListVo} from "../../../service/cody-wizard/vo/is-list-vo";
import ResolveEditor from "../Editor/ResolveEditor";

interface OwnProps {

}

type AnyVoProps = OwnProps & WithWizardStepProps<ImmutableInformationContext> & WithNamespaces;

const AnyVo = (props: AnyVoProps) => {
  const [graph] = useGraph();
  const elementsTree = useElementsTree(props.ctx.board.uid);
  const [metadata, setMetadata] = useState<ValueObjectMetadata>({});
  const [autoFocusSchema, setAutoFocusSchema] = useState(false);
  const [nodeName, setNodeName] = useState(props.ctx.vo? props.ctx.vo.getName() : '');

  const updateMetadata = (meta: ValueObjectMetadata) => {
    if(props.onUpdateMetadata) {
      props.onUpdateMetadata(JSON.stringify(meta, null, 2), true);
    }

    setMetadata(meta);
  }

  useEffect(() => {
    resetForm();

    if(props.ctx.vo) {
      const stateMeta = getVoMetadata(props.ctx.vo);

      setMetadata(stateMeta);
      setNodeName(props.ctx.vo.getName());
    }

  }, [props.ctx.vo])

  const resetForm = () => {
    setMetadata({});
    setAutoFocusSchema(false);
    setNodeName('');
  }

  const handleSubmit = (evt: FormEvent) => {
    evt.preventDefault();
    evt.stopPropagation();
  }

  const handleNameChanged = (newNodeName: string) => {
    if(props.ctx.vo) {
      graph.changeNodeName(props.ctx.vo, newNodeName);
      props.onCtxChanged(updateContext(props.ctx, {vo: props.ctx.vo.changeName(newNodeName)}))
    }
  }

  const handleNsChanged = (newNs: string) => {
    updateMetadata({...metadata, ns: newNs});
  }

  const handleSchemaChanged = (newSchema: Schema) => {
    updateMetadata({...metadata, schema: newSchema});
  }

  const handleQuerySchemaChanged = (newQuerySchema: Schema) => {
    const resolve = metadata.resolve || getDefaultResolveConfigFromQuery(newQuerySchema);
    updateMetadata({...metadata, querySchema: newQuerySchema, resolve});
  }

  const handleQuerySchemaRemoved = () => {
    updateMetadata({...metadata, querySchema: undefined, resolve: undefined});
  }

  const handleResolveChanged = (resolve?: ResolveConfig) => {
    updateMetadata({...metadata, resolve});
  }

  const handleUiSchemaChanged = (newUiSchema: UiSchema) => {
    updateMetadata({...metadata, uiSchema: newUiSchema});
  }

  props.onFinish((): ImmutableInformationContext => {
    if(!props.ctx.vo) {
      resetForm();
      return props.ctx;
    }

    const changedVo = makeNodeFromJs({...convertNodeToJs(props.ctx.vo), ...{name: nodeName, metadata: JSON.stringify(metadata, null, 2)}})
    graph.changeNodeMetadata(changedVo, changedVo.getMetadata() as NodeMetadata);

    resetForm();
    return {...props.ctx, vo: changedVo};
  })

  return <form className='ui form' onSubmit={handleSubmit}>
    {props.mode === 'modal' && <ChangeNodeNameOrDeleteNode node={props.ctx.vo} board={props.ctx.board} onNameChanged={handleNameChanged} disableDelete={true} />}
    <EditorDivider content="Namespace" />
    <NamespaceInput ns={metadata.ns || ''} onNsChanged={handleNsChanged} />
    <EditorDivider content="Schema" />
    <SchemaEditor boardId={props.ctx.board.uid} schema={metadata.schema || Schema.fromString('{}')} onSchemaChanged={handleSchemaChanged} autofocus={autoFocusSchema} />
    <EditorDivider content="Query Schema" />
    <QuerySchemaEditor boardId={props.ctx.board.uid} schema={metadata.querySchema} onSchemaChanged={handleQuerySchemaChanged} onSchemaRemoved={handleQuerySchemaRemoved} />
    <EditorDivider content="Query Resolver" />
    <ResolveEditor boardId={props.ctx.board.uid} nodeSchema={metadata.schema || Schema.fromString('{}')} querySchema={metadata.querySchema || Schema.fromString('{}')} resolve={metadata.resolve} onResolveChanged={handleResolveChanged} />
    <EditorDivider content="UI Schema" />
    <UiSchemaEditor boardId={props.ctx.board.uid} nodeSchema={metadata.schema || Schema.fromString('{}')} schema={metadata.uiSchema || {}} onSchemaChanged={handleUiSchemaChanged} tableSchema={isListVo(props.ctx.vo)} />
    <ButtonGroup>
      <Button basic={true} compact={true} icon="search" className="noborder" content={props.t('insp.cody_wizard.ui_schema_editor.choose_icon')} as="a" href="https://pictogrammers.com/library/mdi/" target="_blank" />
    </ButtonGroup>
    <div style={{marginTop: '100px'}} />
  </form>
};

export default withNamespaces()(AnyVo);

const getDefaultResolveConfigFromQuery = (query: Schema): ResolveConfig => {
  if(!query.isObject() || query.isEmpty()) {
    return {
      where: {
        rule: "always",
        then: {filter: {any: true}}
      }
    }
  }

  const firstProp = query.getObjectProperties()[0];

  if(query.isRequired(firstProp)) {
    return {
      where: {
        rule: "always",
        then: {filter: {eq: {prop: firstProp, value: `query.${firstProp}`}}}
      }
    }
  } else {
    return {
      where: {
        rule: "condition",
        if: `query.${firstProp}`,
        then: {filter: {eq: {prop: firstProp, value: `query.${firstProp}`}}},
        else: {filter: {any: true}}
      }
    }
  }
}
