import {Node, NodeType} from "../../../model/Graph";

export const getSingleSourceOfType = (node: Node, type: NodeType, allowMultiple?: boolean): Node | null => {
  const sources = node.getSources().filter(s => s.getType() === type);

  if(sources.count() === 0) {
    return null;
  }

  if(sources.count() === 1 || allowMultiple) {
    return sources.toArray()[0];
  }

  return null;
}

export const getSingleTargetOfType = (node: Node, type: NodeType, allowMultiple?: boolean): Node | null => {
  const targets = node.getTargets().filter(t => t.getType() === type);

  if(targets.count() === 0) {
    return null;
  }

  if(targets.count() === 1 || allowMultiple) {
    return targets.toArray()[0];
  }

  return null;
}

export const getSourcesOfType = (node: Node, type: NodeType): Node[] => {
  return node.getSources().filter(s => s.getType() === type).toArray();
}

export const getTargetsOfType = (node: Node, type: NodeType): Node[] => {
  return node.getTargets().filter(t => t.getType() === type).toArray();
}
